import React from 'react';
import {AutoComplete} from 'antd';

const {Option} = AutoComplete;

export default class AutoSelect extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			result: [],
			value: props.value || '',
		};
	}

	componentWillReceiveProps(nextProps) {
		if ('value' in nextProps && this.props.value !== nextProps.value) {
			this.setState({
				value: nextProps.value
			});
		}
	}

	componentWillReceiveProps(nextProps) {
		if ('value' in nextProps && this.props.value !== nextProps.value) {
			this.setState({
				value: nextProps.value
			});
		}
	}

	componentDidMount() {
		if (!this.props.url) {
			return;
		}
		this.handleFocus();
	}

	handleChange = (value, e) => {
		if (value === undefined) return false;
		this.setState({value: value === undefined ? "" : value});
		let {onChange} = this.props;
		if (onChange) {
			onChange(value, e);
		}
	}

	handleFocus = () => {
		fetch(this.props.url, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				'Accept': 'application/json',
			},
			body: JSON.stringify({
				query: this.props.query,
				variables: this.props.variables
			})
		})
		.then(response => response.json())
		.then(json => {
			let data = json.data[this.props.type];
            if (this.props.type == "getExpectList") {
	            this.setState({
		            result: data.map((item)=>{
		            	return Object.assign({},{
				            expectId:item.id,
				            name: item.name,
				            completeToday: item.completeToday==undefined||item.completeToday==null?"":item.completeToday,
				            remark: item.remark,
				            completeTotal: item.completeTotal==undefined||item.completeTotal==null?"":item.completeTotal,
				            units: item.units,
				            quantities: item.quantities,
			            });
		            })
	            });
            } else if (this.props.type == "getDayTaskByDate") {
	            this.setState({
		            result: data.filter(item=> !item.id.includes("!"))
	            });
            } else {
	            this.setState({
		            result: data
	            });
            }
		});
	}

	render() {
		const {result, value} = this.state;
		const {keyId} = this.props;
		const type = keyId!==undefined?keyId:"name";
		const children = result.map(item => <Option key={item.id==null?"":item.id} value={item[type]==null?"":item[type]} val={item} title={item.name}>{item.name}</Option>);
		return (
			<AutoComplete style={{width: '100%'}}
			              onSelect={this.handleChange}
			              onChange={this.handleChange}
			             /* filterOption={(inputValue, option) =>
				              option.props.children.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
			              }*/
			              value={value}
			              onFocus={this.handleFocus}
			              >
				{children}
			</AutoComplete>
		);
	}
}



