import React from 'react';
import {observer} from 'mobx-react';
import {
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	InputNumber,
	Select,
	Form, Tag, Divider, TimePicker, AutoComplete
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import Config from "@/config";
import Schemas from "@/utils/Schemas/xmgl";
import AutoSelect from "@/components/common/AutoSelect";
const {Option} = Select;
const confirm = Modal.confirm;

@observer
class Mechanical extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			workerType: [
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
			],
			classification: [{text: ''}, {text: ''}], // 
			machinesNameoptions: [   // 
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
			],
			machinesNameSmallOptions: [   // 
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
			],
		}
	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	getTabeData = () => {
		this.store.getAllMachineByProjectIdAndType();
	}

	onOk = (value) => {

	}

	handleInputNumberChage = (type, number) => {
		this.store.machineFields[type] = number;
	}

	handleAdd = () => {
		this.setState({visible: true, title: 'add'}, () => {
			this.store.machineFields = Object.assign(mobx.toJS(this.store.machineFields),
				{
					time: moment().format("YYYY-MM-DD"),
					startTime: "08:00:00",
					endTime: "18:00:00"
				});
		});
	}

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.machineFields = Object.assign({}, {
				"id": "", "projectId": "", "date": null,"dateEnd":null, "count": 0, "units": "", "type": "",
				"averageHours": null, "remark": "",
				"time": moment().format("YYYY-MM-DD"),
				"startTime": "08:00:00",
				"endTime": "18:00:00",
				"taskId":'',
				"expectResourceId": "",
				"resourceType": "",
				"dayTaskName": "",
			});
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			if (err) {
				return false;
			}
			this.store.machineFields.projectId = this.store.projectId;
			let personFields = mobx.toJS(this.store.machineFields);
			const date = personFields.time+" "+personFields.startTime+":00";
			const dateEnd = personFields.time+" "+personFields.endTime+":00";

			delete personFields.time;
			delete personFields.startTime;
			delete personFields.endTime;

			this.store.machineFields = Object.assign(personFields,
				{
					date,
					dateEnd
				}
			);

			this.store.saveMachine().then((res) => {
				if (res) {
					message.success("")
					this.handleCancle();
					this.getTabeData();
				} else {
					message.success(res&&res.message || "")
				}
			});
		})
	}

	onSelChange = async (type, value, e) => {
		//console.log(value, 'test', e.props.val)
		this.store.machineFields["name"] = value;
		this.store.machineFields["units"] = e.props.val.value;
		this.props.form.setFieldsValue({
			name: value
		});
	}

	onSelChangeType = async (type, value, e) => {
		//console.log(value, this.store.machineFields["type"], e.props.val)
		// 
		if(value!==this.store.machineFields["type"]) {
			//name
			this.store.machineFields["name"] = "";
			this.props.form.setFieldsValue({
				name: ""
			});
		}
		this.store.machineFields["type"] = value;
		this.props.form.setFieldsValue({
			type: value
		});
	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		//console.log(value);
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	showSizeChange = async (current, pageSize) => {
		this.store.machinePageIndex = 1;
		this.store.machinePageSize = pageSize;
		this.store.getAllMachineByProjectIdAndType();
	}

	onPageChange = async (pageNumber) => {
		this.store.machinePageIndex = pageNumber;
		this.store.getAllMachineByProjectIdAndType();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	// 
	handEdit = async (param) => {
		this.setState({
			visible: true,
			title:'edit'
		}, () => {
			delete param.key;

			// 
			let date = param.date;
			let dateEnd = param.dateEnd;
			const time = param.date==null?moment().format("YYYY-MM-DD"):date.split(" ").length&&date.split(" ")[0];
			const startTime = param.date==null?null:date.split(" ").length&&date.split(" ")[1];
			const endTime = param.dateEnd==null?null:dateEnd.split(" ").length&&dateEnd.split(" ")[1];

			this.store.machineFields = Object.assign(mobx.toJS(this.store.machineFields),param,
				{time, startTime, endTime}
				)
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delMachine(param.id).then((res)=>{
					if (res == true) {
						message.success("");
						_this.store.machinePageIndex = 1;
						_this.store.machinePageSize = 10;
						_this.store.getAllMachineByProjectIdAndType();
					} else {
						message.error('');
					}
				})
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	// 
	onDateChange = (type, value, dateString) => {
		//console.log(type, value, dateString, '')
		this.store.machineFields[type] = dateString;
	}

	// 
	handDate = (type, time, timeString) => {
		//console.log(type, time, timeString, '')
		this.store.machineFields[type] = timeString;
	}

	onChangeSelect = (value) => {
		this.props.form.setFieldsValue({
			name: value
		});
		this.store.machineFields["name"] = value;
	}

	onSearch = (val) => {
		console.log('search:', val);
	}

	onSelect = (val) => {
		console.log('select:', val);
	}

	doChange = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.machineFields.taskId = "";
			this.store.machineFields.dayTaskName = "";
			this.store.expectId = "";
		} else {  // 
			this.store.machineFields.taskId = val.id
			this.store.machineFields.dayTaskName = val.name;
			this.store.expectId = val.expectId;
		}
	}

	doChange2 = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.machineFields.resourceType = "";
			this.store.machineFields.expectResourceId = "";
		} else {  // 
			this.store.machineFields.resourceType =  val.type;
			this.store.machineFields.expectResourceId = val.id;
		}
	}

	render() {
		const {workerType,classification, machinesNameoptions, machinesNameSmallOptions} = this.state;
		const {getFieldDecorator} = this.props.form;
		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 5},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 18},
			},
		}
		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'type',
				key: 'type',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'count',
				key: 'count',
				align: 'center',
			},
			/*{
				title: '',
				dataIndex: 'units',
				key: 'units',
			},*/
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text, record) => (
					<div>
						{text==null?"":text}
					</div>
				)
			},
			{
				title: '',
				dataIndex: 'dateEnd',
				key: 'dateEnd',
				align: 'center',
				render: (text, record) => (
					<div>
						{text==null?"":text}
					</div>
				)
			},
			{
				title: '',
				key: 'action',
				render: (text, record) => (
					<span>
            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
               
            </Tag>
            <Divider type="vertical"/>
            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
                 
              </Tag>
          </span>
				),
			}
		];
		const fields = mobx.toJS(this.store.machineFields);
		const machOptions = fields.type!==""&&fields.type==""?machinesNameoptions:machinesNameSmallOptions;
		const options = machOptions.map( (group, index) => (
			<Option key={index} value={group.text}>
				{group.text}
			</Option>
		));

		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" onClick={this.handleAdd}></Button>
				<Table columns={columns}
				       pagination={false}
				       size="middle"
				       className="xlj-table mt25"
				       dataSource={mobx.toJS(this.store.machineTableData)}
				/>
				<div style={styles.pagination}>
					<Pagination
						current={this.store.machinePageIndex}
						pageSize={this.store.machinePageSize}
						//pageSizeOptions={["20", "50", "100", "500"]}
						total={this.store.machineTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showTotal={this.showTotal}
						showQuickJumper/>
				</div>
				<Modal
					title={this.state.title == "add" ? "" : ""}
					visible={this.state.visible}
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form {...formItemLayout}>
						<Form.Item
							wrapperCol={{
							           xs: {span: 24,},
							           sm: {span: 24,},
						           }}
						           style={{marginBottom: 0}}>
							<div>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 10},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 14},
									}}
									style={{display: 'inline-block', width: '50%'}}
									label="">
									{getFieldDecorator('dayTaskName', {
										initialValue: fields.dayTaskName,
										rules: [{
											required: true,
											message: ''
										}],
									})(
										<AutoSelect
											url={Config.xmgl.getDayTaskByDate}
											query={Schemas.xmgl.getDayTaskByDate}
											variables={{
												projectId: this.store.projectId,
												data: fields.time == null?"":fields.time
											}}
											onChange={this.doChange}
											value={fields.dayTaskName == null || fields.dayTaskName == "" ? "" : fields.dayTaskName}
											type="getDayTaskByDate"
										/>
									)}
								</Form.Item>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 10},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 12}
									}}
									style={{display: 'inline-block', width: '50%'}}
									label="">
									{getFieldDecorator('expectResourceId', {
										initialValue: fields.expectResourceId == null || fields.expectResourceId == "" ? "" : fields.expectResourceId,
										rules: [{
											required: true,
											message: ''
										}],
									})(
										<AutoSelect
											url={Config.xmgl.getExpectResourceList}
											query={Schemas.xmgl.getExpectResourceList}
											variables={{
												expectId: this.store.expectId,
												type: ""
											}}
											onChange={this.doChange2}
											value={fields.expectResourceId == null || fields.expectResourceId == "" ? "" : fields.expectResourceId}
											type="getExpectResourceList"
											keyId={"id"}
										/>
									)}
								</Form.Item>
							</div>
						</Form.Item>
						<Form.Item label="">
							{getFieldDecorator('type', {
								initialValue: fields.type,
								rules: [{
									required: true,
									message: ''
								}],
							})(
								<div className="w">
									<Select
										value={fields.type}
										onChange={this.onSelChangeType.bind(this, 'type')}
										placeholder="...">
										{classification.map((item, index) => {
											return (<Option key={'classification' + index} value={item.text} val={item}>{item.text}</Option>)
										})}
									</Select>
								</div>
							)}
						</Form.Item>
						<Form.Item label="">
							{getFieldDecorator('name', {
								initialValue: fields.name,
								rules: [{
									required: true,
									message: ''
								}],
							})(
									<AutoComplete
										disabled={fields.type==""||fields.type==null?true:false}
										dataSource={options}
										style={{width: "100%"}}
										onSelect={this.onSelect}
										onSearch={this.onSearch}
										onChange={this.onChangeSelect}
										filterOption={(input, option) =>
											option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
										}
										placeholder=""
									/>
							)}
						</Form.Item>

						{/*<Select
							onChange={this.onSelChange.bind(this, 'name')}
							placeholder="...">
							{workerType.map((item, index) => {
								return (<Option key={'type' + index} value={item.text} val={item}>{item.text}</Option>)
							})}
						</Select>*/}
						{/*<Form.Item label="">
							{getFieldDecorator('units', {})(<span>{fields.units}</span>)}
						</Form.Item>*/}
						<Form.Item label={``}>
							{getFieldDecorator('count', {
								initialValue: fields.count,
								rules: [{required: true, message: ''}],
							})(<InputNumber
								style={{width: '100%'}}
								min={0}
								onChange={this.handleInputNumberChage.bind(this, "count")}
								formatter={this.limitDecimals}
								parser={this.limitDecimals}/>)}
						</Form.Item>
						<Form.Item label="">
							{getFieldDecorator('time', {
								initialValue: fields.time == null ? "" : moment(fields.time, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={'YYYY-MM-DD'}
								onChange={this.onDateChange.bind(this, 'time')}
								style={{width: '100%'}}/>)}
						</Form.Item>
						<Form.Item label=""
						           labelCol={{
							           xs: {span: 24},
							           sm: {span: 5},
						           }}
						           wrapperCol={{
							           xs: {span: 24},
							           sm: {span: 18},
						           }}
						           style={{marginBottom: 0}}>
							<div>
								<Form.Item
									style={{display: 'inline-block', width: 'calc(50% - 12px)'}}
								>{getFieldDecorator('startTime', {
									initialValue: fields.startTime == null ? "" : moment(fields.startTime, 'HH:mm'),
									rules: [{required: true, message: ''}],
								})(
									<TimePicker
										style={{width: '100%'}}
										format={'HH:mm'}
										value={fields.startTime == null ? "" : moment(fields.startTime, 'HH:mm')}
										onChange={this.handDate.bind(this, 'startTime')}
										placeholder=""
									/>
								)}
								</Form.Item>
								<span style={{display: 'inline-block', width: '24px', textAlign: 'center'}}>-</span>
								<Form.Item
									style={{display: 'inline-block', width: 'calc(50% - 12px)'}}
								>
									{getFieldDecorator('endTime', {
										initialValue: fields.endTime == null ? "" : moment(fields.endTime, 'HH:mm'),
										rules: [{required: true, message: ''}],
									})(
										<TimePicker
											style={{width: '100%'}}
											format={'HH:mm'}
											value={fields.endTime == null ? "" : moment(fields.endTime, 'HH:mm')}
											onChange={this.handDate.bind(this, 'endTime')}
											placeholder=""
										/>
									)}
								</Form.Item>
							</div>
						</Form.Item>



						{/*<Form.Item label={``}>
							{getFieldDecorator(`time`, {
								initialValue: fields.date==null?[]:[moment(fields.date, 'YYYY-MM-DD HH:mm:ss'), moment(fields.dateEnd, 'YYYY-MM-DD HH:mm:ss')],
								rules: [
									{
										required: true,
										message: '',
									},
								],
							})(
								<div className="w">
									<RangePicker
										value={ fields.date==null?[]:[moment(fields.date, 'YYYY-MM-DD HH:mm:ss'), moment(fields.dateEnd, 'YYYY-MM-DD HH:mm:ss')]}
										showTime={{
											format: "HH:mm:ss"
										}}
										placeholder={['','']}
										style={{width: '100%'}}
										format="YYYY-MM-DD HH:mm:ss"
										onChange={this.ondateChange}
										onOk={this.onOk}
									/>

								</div>
							)}
						</Form.Item>*/}
					</Form>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Mechanical);
export default form;






