import React from 'react';
import {observer, inject} from 'mobx-react';
import {
	Divider,
	Button,
	Tag,
	Table,
	DatePicker,
	Pagination,
	Modal,
	Form,
	Input,
	message,
	InputNumber,
	Col,
	Row,
} from 'antd';
import * as mobx from "mobx";
import moment from 'moment';
import Config from "@/config";
import AutoSelect from "@/components/common/AutoSelect";
import MyUpload from '@/components/common/MyUpload';

import Schemas from "@/utils/Schemas/xmgl";

const confirm = Modal.confirm;

@observer
class MateriaModal extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			title: '',
			fileList: []
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	componentWillUnmount() {

	}

	getTabeData = () => {
		this.store.getMaterialList();
	}

	showSizeChange = async (current, pageSize) => {
		this.store.cardpageIndex = 1;
		this.store.cardpageSize = pageSize;
		this.store.getMaterialList();
	}

	onChange = async (pageNumber) => {
		this.store.cardpageIndex = pageNumber;
		this.store.getMaterialList();
	}

	// 
	handAdd = () => {
		this.setState({
			visible: true,
			title: ''
		}, () => {
			this.store.material = Object.assign(mobx.toJS(this.store.material),
				{deliveryTime: moment().format("YYYY-MM-DD"), projectId: this.store.projectId})
		})
	}

	// 
	handEdit = (param) => {
		this.setState({
			visible: true,
			title: ''
		}, () => {
			this.store.getMaterial(param).then((res)=>{
				if (res) {

					this.store.material = Object.assign({}, res, {
						attachments: res.attachments !== null ? res.attachments.map((vitem, index) => {
							return Object.assign({}, {
								uid: vitem.fileId,
								name: vitem.fileName,
								status: 'done',
								url: vitem.filePath,
								fileId: vitem.fileId,
								fileName:vitem.fileName,
								filePath: vitem.filePath,
								id: vitem.fileId
							})
						}) : []
					});

					this.store.material.projectId = this.store.projectId;
				}
			});
		})
	}

	// 
	handDelete = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delMaterial(param).then((res)=>{
					if (res.code == 1) {
						message.success("")
						_this.cardpageIndex = 1;
						_this.cardpageSize = 10;
						_this.store.getMaterialList();
					} else {
						message.success(res.message||"")
					}
				});
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	// 
	handleSubmit = e => {
		let _this = this;
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			//console.log( fieldsValue);

			if (err) {
				return false;
			}
			const values = {
				...fieldsValue,
				'deliveryTime': fieldsValue['deliveryTime'].format('YYYY-MM-DD'),
			};

			// 
			let array = JSON.parse(JSON.stringify(this.store.material.attachments))
			let arr = [];
			array.length&&array.map((item,index)=>{
				arr.push({
					fileId: item.fileId,
					fileName:item.fileName,
					filePath: item.filePath,
					//id: item.id
				})
			});

			let material = JSON.parse(JSON.stringify(this.store.material));
			this.store.material = Object.assign(material, values);

			_this.store.saveMaterial(
				Object.assign(mobx.toJS(this.store.material), {
					attachments: arr
				})
			).then((res)=>{
				  if (res.code == 1) {
				  	message.success("")
					  _this.handleCancel();
					  _this.cardpageIndex = 1;
					  _this.cardpageSize = 10;
					  _this.store.getMaterialList();
					} else {
						message.success(res.message||"")
					}
			});
		});
	};

	// 
	handleCancel = () => {
		this.setState({
			visible: false
		}, ()=> {
			this.store.material = Object.assign({}, {
				id:null,
				projectId:this.store.projectId,
				name:"",
				spec:"",
				manufacturer:"",
				certificateNumber:"",
				num:null,
				item:null,
				result:null,
				remark:"",
				reportId:"",
				totalNum:"",
				deliveryTime: moment().format("YYYY-MM-DD"),
				units:"",
				attachments:[],
				"taskId":'',
				"expectResourceId": "",
				"resourceType": "",
				"dayTaskName": "",
			});
			this.props.form.resetFields();
		})
	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		console.log(value);
		if(typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	handleChange = info => {
		//console.log(info, '0')
		const status = info.file.status;
		if (status === 'done') {
			// 
			//message.success(`${info.file.name}`);
		} else if (status === 'error') {
			message.error(`${info.file.name}`);
		} else if (status === 'removed') {
			//console.log('delete')
		}
		let fileList = [...info.fileList];
		fileList = JSON.parse(JSON.stringify(fileList));
		fileList = fileList.filter((file) => {
			if (file.status === 'error') {
				return false;
			}
			return true;
		});
		fileList = fileList.map(file => {
			if (file.response) {
				file.url = file.response.body[0].fileURL;
				file.fileId = file.response.body[0].fileId;
				file.id = file.response.body[0].fileId;
				file.fileName = file.response.body[0].fileName;
				file.filePath = file.response.body[0].fileURL;
			}
			return file;
		});
		fileList = fileList.filter((file) => {
			if (file.response) {
				return file.response.status === 'ok';
			}
			return true;
		});

		this.setState({
			fileList: Object.assign([], [...fileList])
		}, ()=>{
			// 
			let data = this.state.fileList.map((item,index)=>{
				return {
					uid: item.uid,
					name: item.fileName,
					status: 'done',
					url: item.fileURL,
					fileId: item.fileId,
					fileName:item.fileName,
					filePath: item.filePath,
					id: item.fileId
				}
			})

			this.store.material.attachments = Object.assign([], data);
		});
	};

	// 
	handleAttment = (params) => {
		// 
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.store.material.attachments = Object.assign([], arr);
	}

	doChange = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.material.taskId = "";
			this.store.material.dayTaskName = "";
			this.store.expectId = "";
		} else {  // 
			this.store.material.taskId = val.id
			this.store.material.dayTaskName = val.name;
			this.store.expectId = val.expectId;
		}
	}

	doChange2 = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.material.resourceType = "";
			this.store.material.expectResourceId = "";
		} else {  // 
			this.store.material.resourceType =  val.type;
			this.store.material.expectResourceId = val.id;
		}
	}

	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'spec',
				key: 'spec',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'units',
				key: 'units',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'totalNum',
				key: 'totalNum',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'deliveryTime',
				key: 'deliveryTime',
				align: 'center',
				render:(text) => (
					<span>{text==null?"":moment(text).format("YYYY-MM-DD")}</span>
				),
			},
			{
				title: '',
				dataIndex: 'manufacturer',
				key: 'manufacturer',
				align: 'center',
			},
			/*{
				title: '',
				dataIndex: 'certificateNumber',
				key: 'certificateNumber',
				className: 'tc'
			},*/
			{
				title: '',
				key: 'action',
				render: (text, record) => (
					<span>
            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record.id)}>
               
            </Tag>
            <Divider type="vertical"/>
            <Tag color={'red'} className="pointer" onClick={this.handDelete.bind(this, record.id)}>
                 
              </Tag>
          </span>
				),
			}
		];
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 9},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 15},
			},
		};
		const {visible,title} = this.state;
		const fields = mobx.toJS(this.store.material);
		const dateFormat = "YYYY-MM-DD";
		const props = {
			data: {
				type: 1,    // 
				sourceId: this.store.material.id // id
			},
			name: 'file',
			multiple: true,
			action:Config.management.multiUpload,
			onChange: this.handleChange,
			fileList: mobx.toJS(this.store.material.attachments)
		}

		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" className="mb10" onClick={this.handAdd}>
					
				</Button>
				<Table columns={columns}
				       size="middle"
				       className="xlj-table"
							 dataSource={mobx.toJS(this.store.cardtableData)}
							 pagination={false}
					//loading={loading}
				/>
				<Pagination
					style={{margin: '20px', textAlign: 'right'}}
					current={this.store.cardpageIndex}
					pageSize={this.store.cardpageSize}
					total={this.store.cardtotalCount}
					showSizeChanger
					onShowSizeChange={this.showSizeChange}
					onChange={this.onChange}
					showLessItems={true}
					showQuickJumper/>
				<Modal
					title={title}
					visible={visible}
					width="50%"
					onCancel={this.handleCancel}
					onOk={this.handleSubmit.bind(this)}
					maskClosable={false}
				>
					<Form {...formItemLayout}>
						<Row className={'clearfix'}>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('dayTaskName', {
										initialValue: fields.dayTaskName,
										rules: [{required: true, message: ''}],
									})(
										<AutoSelect
											url={Config.xmgl.getDayTaskByDate}
											query={Schemas.xmgl.getDayTaskByDate}
											variables={{
												projectId: this.store.projectId,
												data: fields.time == null?"":fields.time
											}}
											onChange={this.doChange}
											value={fields.dayTaskName == null || fields.dayTaskName == "" ? "" : fields.dayTaskName}
											type="getDayTaskByDate"
										/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('expectResourceId', {
										initialValue: fields.expectResourceId == null || fields.expectResourceId == "" ? "" : fields.expectResourceId,
										rules: [{required: true, message: ''}],
									})(<AutoSelect
										url={Config.xmgl.getExpectResourceList}
										query={Schemas.xmgl.getExpectResourceList}
										variables={{
											expectId: this.store.expectId,
											type: ""
										}}
										onChange={this.doChange2}
										value={fields.expectResourceId == null || fields.expectResourceId == "" ? "" : fields.expectResourceId}
										type="getExpectResourceList"
										keyId={"id"}
									/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('name', {
										initialValue: fields.name,
										rules: [{required: true, message: ''}],
									})(<Input/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('spec', {
										initialValue: fields.spec,
										rules: [{required: false, message: ''}],
									})(<Input/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('units', {
										initialValue: fields.units,
										rules: [{required: true, message: ''}],
									})(<Input/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('totalNum', {
										initialValue: fields.totalNum,
										rules: [{required: true, message: ''}],
									})(<InputNumber
										style={{width: '100%'}}
										formatter={this.limitDecimals}
										parser={this.limitDecimals}/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="	">
									{getFieldDecorator('deliveryTime', {
										initialValue: fields.deliveryTime==null?"":moment(fields.deliveryTime, 'YYYY-MM-DD'),
										rules: [{required: true, message: ''}],
									})(<DatePicker
										dateFormat={dateFormat}
										style={{width: '100%'}}/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('manufacturer', {
										initialValue: fields.manufacturer,
										rules: [{required: false, message: ''}],
									})(<Input/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item
									label={``}>
									<MyUpload
										length={9}
										saveAttachment={this.handleAttment.bind(this)}
										disabled={false}
										multiple={true}
										listType="text"
										ossData={this.props.ossData}
										fileList={mobx.toJS(this.store.material.attachments)}
									/>
								</Form.Item>
							</Col>
						</Row>
						{/*<Form.Item label="">
							{getFieldDecorator('certificateNumber', {
								initialValue: fields.certificateNumber,
								rules: [{required: false, message: ''}],
							})(<Input/>)}
						</Form.Item>*/}
					</Form>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(MateriaModal);
export default form;




