import React from 'react';
import {observer} from 'mobx-react';
import {
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	InputNumber,
	Select,
	Form, Tag, Divider, Input,
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';

const {RangePicker} = DatePicker;
const {Option} = Select;
const confirm = Modal.confirm;
const {TextArea} = Input;

@observer
class Environment extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,

		}
	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	getTabeData = () => {
		this.store.getEnvironmentList();
	}

	// 
	handleAdd = () => {
		this.setState({visible: true, title: 'add'},
			() => {
				this.store.environmentFields = Object.assign(mobx.toJS(this.store.environmentFields), {
					date: moment().format("YYYY-MM-DD")
				})
				this.store.getWeatherByDate();
			});
	}

	// 
	handEdit = async (param) => {
		this.setState({
			visible: true,
			title: 'edit'
		}, () => {
			delete param.key;
			this.store.environmentFields = Object.assign(mobx.toJS(this.store.environmentFields), param)
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delEnvironment(param.id).then((res) => {
					if (res == true) {
						message.success("!");
						_this.store.environmentPageIndex = 1;
						_this.store.environmentPageSize = 10;
						_this.store.getEnvironmentList();
					} else {
						message.error('!');
					}
				})
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.environmentFields = {
				"id": null,
				"projectId": "",
				"date": null,
				"text": "",
				// "remark":""
			};
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		this.props.form.validateFields((err, fieldsValue) => {
			if (err) {
				return false;
			}
			this.store.environmentFields.projectId = this.store.projectId;
			this.store.saveEnvironment().then((res) => {
				if (res) {
					message.success("")
					this.handleCancle();
					_this.store.environmentPageIndex = 1;
					_this.store.environmentPageSize = 10;
					_this.store.getEnvironmentList();
				} else {
					message.success(res && res.message || "")
				}
			});
		})
	}

	onDateChange = (type, value, dateString) => {
		this.store.environmentFields[type] = dateString;
		if (type === "date") this.store.getWeatherByDate();
	}


	handleChange = (type, e) => {
		this.store.environmentFields[type] = e.target.value;
	}

	showSizeChange = async (current, pageSize) => {
		this.store.environmentPageIndex = 1;
		this.store.environmentPageSize = pageSize;
		this.store.getEnvironmentList();
	}

	onPageChange = async (pageNumber) => {
		this.store.environmentPageIndex = pageNumber;
		this.store.getEnvironmentList();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}


	render() {
		const {getFieldDecorator} = this.props.form;
		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 5},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 18},
			},
		}
		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text, record) => (
					<div>
						{text == null ? "" : text}
					</div>
				)
			},
			{
				title: '',
				dataIndex: 'text',
				key: 'text',
				align: 'left',
			},
			// {
			// 	title: '',
			// 	dataIndex: 'remark',
			// 	key: 'remark',
			// },
			{
				title: '',
				key: 'action',
				fixed: 'right',
				align: 'left',
				width: 150,
				render: (text, record) => (
					<span>
            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
               
            </Tag>
            <Divider type="vertical"/>
            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
                 
              </Tag>
          </span>
				),
			}
		];
		const fields = mobx.toJS(this.store.environmentFields);
		const dateFormat = "YYYY-MM-DD";


		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" onClick={this.handleAdd}></Button>
				<Table columns={columns}
				       pagination={false}
				       size="middle"
				       className="xlj-table mt25"
				       dataSource={mobx.toJS(this.store.environmentTableData)}
				/>
				<div style={styles.pagination}>
					<Pagination
						current={this.store.environmentPageIndex}
						pageSize={this.store.environmentPageSize}
						//pageSizeOptions={["20", "50", "100", "500"]}
						total={this.store.environmentTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showTotal={this.showTotal}
						showQuickJumper/>
				</div>
				<Modal
					title={this.state.title == "add" ? "" : ""}
					visible={this.state.visible}
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form {...formItemLayout}>
						<Form.Item label="">
							{getFieldDecorator('date', {
								initialValue: fields.date == null ? "" : moment(fields.date, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={dateFormat}
								onChange={this.onDateChange.bind(this, 'date')}
								style={{width: '100%'}}/>)}
						</Form.Item>
						<Form.Item
							label="">
							{getFieldDecorator('text', {
								initialValue: fields.text,
								rules: [{required: true, message: ''}],
							})(
								<TextArea autosize={{minRows: 4}}
								          placeholder=""
								          onChange={this.handleChange.bind(this, 'text')}
								          value={fields.text}
								/>)}
						</Form.Item>
						{/* <Form.Item
							label="">
							{getFieldDecorator('remark', {
								initialValue: fields.method,
								rules: [{required: false, message: ''}],
							})(
								<TextArea autosize={{minRows: 4}}
								          placeholder=""
								          onChange={this.handleChange.bind(this, 'remark')}
								          value={fields.remark}
								/>)}
						</Form.Item> */}
					</Form>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Environment);
export default form;






