/*
* 
* */
import React from 'react';
import {
	Form,
	Input,
	Icon,
	Table,
	Popconfirm,
	ConfigProvider,
	InputNumber,
} from 'antd';
import {v4 as uuidv4} from 'uuid';

import Config from '@/config';
import Schemas from "@/utils/Schemas/xmgl";
import AutoSelect from "@/components/common/AutoSelect";

const {TextArea} = Input;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			delete values.name;
			handleSave({...record, ...values});
		});
	};

	doChange = (value, e) => {
		const {val} = e.props;
		const {record, handleSave} = this.props;
		if (val == undefined) {  // 
			let obj = {name: value, expectId: ""};
			handleSave({...record, ...obj});
		} else {  // 
			let obj = {
				expectId: val.expectId,
				name: val.name,
				completeToday: val.completeToday,
				remark: val.remark,
				completeTotal: val.completeTotal,
				units: val.units,
				quantities: val.quantities
			}
			handleSave({...record, ...obj});
		}
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, handleDelete, date, projectId} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'completeToday' || dataIndex == 'completeTotal'||dataIndex == 'quantities'||dataIndex == "completeTodayTag") {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex],
					})(
						<InputNumber
							//style={{width: '60px'}}
							ref={node => (this.input = node)}
							onChange={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'remark') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea rows={2}
						          ref={node => (this.input = node)}
						          onChange={this.save}
						          onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'name') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<div className="pr">
							<Popconfirm title="?" onConfirm={() => handleDelete(record)}>
								<Icon
									style={{
										position: 'absolute',
										left: '-30px',
										margin: '16px 0',
										color: '#4BA4BE',
										cursor: 'pointer',
										fontSize: '18px'
									}}
									type="minus-circle"/>
							</Popconfirm>
							<AutoSelect
								url={Config.xmgl.getExpectList}
								query={Schemas.xmgl.getExpectList}
								variables={{
									projectId: projectId,
									data: date
								}}
								onChange={this.doChange}
								value={record[dataIndex] == null || record[dataIndex] == "" ? "" : record[dataIndex]}
								type="getExpectList"
							/>
						</div>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else {
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							style={{width: '60px'}}
							ref={node => (this.input = node)}
							onChange={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

class ProEditTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			dataSource: [],
			addData: {
				id: "",
				planId: "",
				expectId: "",
				name: "",
				completeToday: 0,
				remark: "",
				completeTotal: 0,
				units: "",
				quantities: 0,
				completeTodayTag: 0
			},
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (JSON.stringify(nextProps.dataSource) != JSON.stringify(this.props.dataSource)) {
			this.setState({
				dataSource: nextProps.dataSource
			});
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource
		});
	}

	// 
	handleAdd = () => {
		const {dataSource, addData} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		const {planId} = this.props;
		const newData = Object.assign(addData, {id: "!" + uuidv4(), planId});
		this.setState({
			dataSource: [...source, newData],
		}, () => {
			this.props.updateData("dayTasks", this.state.dataSource);
		});
	}

	// 
	handleDelete = (record) => {
		const {id} = record;
		const {dataSource} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		this.setState({
			dataSource: source.filter(item => item.id !== id)
		}, () => {
			this.props.updateData("dayTasks", this.state.dataSource);
		});
	};

	// 
	handleSave = (row) => {
		const {dataSource} = this.state;
		const source = JSON.parse(JSON.stringify(dataSource));
		const index = source.findIndex(item => row.id === item.id);
		const item = source[index];
		source.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: source}, () => {
			this.props.updateData("dayTasks", this.state.dataSource);
		});
	}

	render() {
		const {dataSource} = this.state;
		const {projectId, date} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
				editable: true,
			},
			{
				title: '',
				dataIndex: 'expectId',
				key: 'expectId',
				align: 'center',
				width: '60px',
				render: (text) => (
					<div>
						{text == null || text == "" ? "" : ""}
					</div>
				),
			},
			{
				title: '',
				dataIndex: 'quantities',
				key: 'quantities',
				align: 'center',
				editable: false,
				//width: '60px',
			},
			{
				title: '',
				dataIndex: 'completeTodayTag',
				key: 'completeTodayTag',
				align: 'center',
				editable: true,
				//width: '60px',
			},
			{
				title: '',
				dataIndex: 'units',
				key: 'units',
				align: 'center',
				editable: true,
				//width: '50px',
			},
			{
				title: '',
				dataIndex: 'completeToday',
				key: 'completeToday',
				align: 'center',
				editable: true,
				//width: '60px',
			},
			{
				title: '',
				dataIndex: 'completeTotal',
				key: 'completeTotal',
				align: 'center',
				width: '60px',
			},
			{
				title: '',
				dataIndex: 'remark',
				key: 'remark',
				align: 'center',
				editable: true,
				//width: '80px',
			}
		];
		const colu = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
					projectId: projectId,
					date: date
				}),
			};
		});

		return (
			<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="mt10"
					pagination={false}
					columns={colu}
					size={"middle"}
					bordered={true}
					dataSource={dataSource}
					rowKey={record => record.id}
				/>
				<Icon onClick={this.handleAdd}
				      style={{margin: '16px 0', color: '#4BA4BE', fontSize: '18px'}}
				      type="plus-circle"/>
			</ConfigProvider>)
	}
}

export default ProEditTable;






