import React from 'react';
import {observer} from 'mobx-react';
import {
	Typography,
	Card,
	Switch,
	message,
	DatePicker,
	Button,
	Form,
	Input
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import AsyncSelectMuti from '@/components/common/AsyncSelectMutiold'
import Config from "@/config";
import Schemas from "@/utils/Schemas/authorization";

const {Title} = Typography;

@observer
class ItemSetting extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {}
	}

	componentDidMount() {
		this.store.getNotifyProject();
	}

	// 
	onSelChange = (type, value) => {
		//console.log(type, value, '123')
		this.store.notifyfield[type] = Object.assign([], value);
		this.props.form.setFieldsValue({
			userIds: value
		});
	}

	handleChage = (type, e) => {
		this.store.notifyfield[type] = e.target.value;
	}

	// 
	handleSubmit = e => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			//console.log(fieldsValue, '11')
			if (err) {
				return;
			}
			this.store.notifyfield = Object.assign(mobx.toJS(this.store.notifyfield), {
				beginTime: fieldsValue["beginTime"].format('YYYY-MM-DD'),
				endTime: fieldsValue["endTime"].format('YYYY-MM-DD'),
				planBeginTime: fieldsValue["planBeginTime"].format('YYYY-MM-DD'),
				planEndTime: fieldsValue["planEndTime"].format('YYYY-MM-DD'),
				notifys: [{wecat:fieldsValue["wecat"]},{email: fieldsValue["email"]}],
				userIds: fieldsValue["userIds"],
				projectId: this.store.projectId
			});
			let params = JSON.parse(JSON.stringify(this.store.notifyfield));
			delete params.wecat;
			delete params.email;
			this.store.saveNotifyProject(params).then((res) => {
				if (res == true) {
					message.success("")
				} else {
					message.success("")
				}
			});
		});
	};

	render() {
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 18},
			},
		};
		const dateFormat = "YYYY-MM-DD";
		const fields = mobx.toJS(this.store.notifyfield);

		return (
			<div className="w90">
				<Title className="tfc" style={{fontWeight: '500'}} level={4}>{this.store.parojectName}</Title>
				<Form {...formItemLayout} onSubmit={this.handleSubmit}>
					<Card title="" bordered={false} style={{width: '100%', marginTop: '0px'}}>
						<Form.Item label="">
							{getFieldDecorator('beginTime', {
								initialValue: fields.beginTime == null ? "" : moment(fields.beginTime, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={dateFormat}
								style={{width: '300px'}}/>)}
						</Form.Item>
						<Form.Item className="mt15" label="">
							{getFieldDecorator('endTime', {
								initialValue: fields.endTime == null ? "" : moment(fields.endTime, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={dateFormat}
								style={{width: '300px'}}/>)}
						</Form.Item>
						<Form.Item className="mt15" label="">
							{getFieldDecorator('planBeginTime', {
								initialValue: fields.planBeginTime == null ? "" : moment(fields.planBeginTime, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={dateFormat}
								style={{width: '300px'}}/>)}
						</Form.Item>
						<Form.Item className="mt15" label="">
							{getFieldDecorator('planEndTime', {
								initialValue: fields.planEndTime == null ? "" : moment(fields.planEndTime, 'YYYY-MM-DD'),
								rules: [{required: true, message: ''}],
							})(<DatePicker
								dateFormat={dateFormat}
								style={{width: '300px'}}/>)}
						</Form.Item>
						<Form.Item className="mt15" label={``}>
							{getFieldDecorator('manager', {
								initialValue: fields.manager,
								rules: [{required: true, message: ''}],
							})(<Input style={{width: '300px'}}
								onChange={this.handleChage.bind(this, "manager")}
							/>)}
						</Form.Item>
					</Card>
					<Card title="" bordered={false} style={{width: '100%'}} className="mt15">
						<Form.Item label="">
							{getFieldDecorator('email', { valuePropName: 'checked', initialValue: fields.email})(<Switch />)}
						</Form.Item>
						<Form.Item label="">
							{getFieldDecorator('wecat', { valuePropName: 'checked',  initialValue: fields.wecat })(<Switch />)}
						</Form.Item>
						<Form.Item label="">
							{getFieldDecorator('userIds', {
								initialValue: mobx.toJS(fields.userIds),
								rules: [{required: true, message: ''}],
							})(
								<div className="w">
									<AsyncSelectMuti
										url={Config.authorization.getUserInfoListByRoleCode}
										style={{width: '100%'}}
										onChange={this.onSelChange.bind(this, 'userIds')}
										value={mobx.toJS(fields.userIds)}
										query={Schemas.authorization.getUserInfoListByRoleCode}
									/>
								</div>
							)}
						</Form.Item>
					</Card>
					<Form.Item
						wrapperCol={{
							xs: {span: 24, offset: 0},
							sm: {span: 24, offset: 0},
						}}
					>
						<div className="tfc mt25">
							<Button type="primary" htmlType="submit">
								
							</Button>
						</div>
					</Form.Item>
				</Form>
			</div>
		)
	}
}


const form = Form.create({})(ItemSetting);
export default form;






