import React from 'react';
import {observer} from 'mobx-react';
import {
	Row,
	Col,
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	Input,
	InputNumber,
	Radio,
	Form,
	Tag, Divider, Icon, Upload,
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import {List} from "immutable";
import Config from "@/config";
import MyUpload from '@/components/common/MyUpload';


const {TextArea} = Input;
const confirm = Modal.confirm;

function getBase64(file) {
	return new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = error => reject(error);
	});
}

@observer
class Epidemic extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			fileList: [],
			previewVisible: false,
			previewImage: '',
		}
	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	getTabeData = () => {
		this.store.getEpidemicList();
	}

	// 
	handleAdd = () => {
		this.setState({visible: true,
			title: 'add'}, () => {

		});
	}

	// 
	onDateChange = (type, value, dateString) => {
		this.store.epidemicFields[type] = dateString;
	}

	// 
	handleChage = (type, e) => {
		if (type=="normalTemperature") {
          if (e.target.value=="true") {
	          this.store.epidemicFields["normalTemperatureText"] = "";
	          this.props.form.setFieldsValue({
		          normalTemperatureText: "",
	          })
          }
		  this.store.epidemicFields[type] = e.target.value;
		} else if (type=="protect") {
			if (e.target.value=="true") {
				this.store.epidemicFields["protectText"] = "";
				this.props.form.setFieldsValue({
					protectText: "",
				})
			}
			this.store.epidemicFields[type] = e.target.value;
		} else if (type=="disinfection") {
			if (e.target.value=="true") {
				this.store.epidemicFields["disinfectionText"] = "";
				this.props.form.setFieldsValue({
					disinfectionText: "",
				})
			}
			this.store.epidemicFields[type] = e.target.value;
		} else {
			this.store.epidemicFields[type] = e.target.value;
		}
	}

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.epidemicFields = Object.assign({}, {
				"id": null,
				"date": moment().format("YYYY-MM-DD"),
				"normalTemperature": "true",
				"protect": "true",
				"disinfection": "true",
				"normalTemperatureText":"",
				"protectText":"",
				"disinfectionText":"",
				"attachments":[]
			});
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			//console.log(fieldsValue, mobx.toJS(this.store.planFields), '8');
			if (err) {
				return false;
			}

			// 
			let array = JSON.parse(JSON.stringify(this.store.epidemicFields.attachments))
			let arr = [];
			array.length&&array.map((item,index)=>{
				arr.push({
					fileId: item.fileId,
					fileName:item.fileName,
					filePath: item.filePath
				})
			});

			const {normalTemperature,protect,disinfection} = mobx.toJS(this.store.epidemicFields);

			this.store.saveEpidemic(
				Object.assign(mobx.toJS(this.store.epidemicFields), {
					attachments: arr,
					projectId: this.store.projectId,
					normalTemperature: normalTemperature=="true"?true:false,
					protect: protect=="true"?true:false,
					disinfection: disinfection=="true"?true:false,
				})
			).then((res) => {
				if (res) {
					message.success("")
					this.getTabeData();
					this.handleCancle();
				} else {
					message.success(res.message || "")
				}
			});
		})
	}

	showSizeChange = async (current, pageSize) => {
		this.store.epidemicPageIndex = 1;
		this.store.epidemicPageSize = pageSize;
		this.store.getEpidemicList();
	}

	onPageChange = async (pageNumber) => {
		this.store.epidemicPageIndex = pageNumber;
		this.store.getEpidemicList();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	// 
	handEdit = async (param) => {
		this.setState({
			visible: true,
			title: 'edit'
		}, () => {
			delete param.key;

			// 
			const params = {
				projectId: param.projectId,
				date: param.date
			}
			this.store.getEpidemicByDate(params).then((res)=>{
				this.store.epidemicFields = Object.assign({}, res, {
					"projectId": res.projectId,
					"id": res.id,
					"date": res.date,
					"normalTemperatureText":res.normalTemperatureText,
					"protectText":res.protectText,
					"disinfectionText":res.disinfectionText,
					"normalTemperature": res.normalTemperature.toString(),
					"protect": res.protect.toString(),
					"disinfection": res.disinfection.toString(),
					"attachments": res.attachments !== null ? res.attachments.map((vitem, index) => {
						return Object.assign({}, {
							uid: vitem.fileId,
							name: vitem.fileName,
							status: 'done',
							url: vitem.filePath,
							fileId: vitem.fileId,
							fileName:vitem.fileName,
							filePath: vitem.filePath,
							id: vitem.fileId
						})
					}) : []
				});

			});
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delEpidemic(param.id).then((res) => {
					if (res == true) {
						message.success("");
						_this.store.epidemicPageIndex = 1;
						_this.store.epidemicPageSize = 10;
						_this.store.getEpidemicList();
					} else {
						message.error('');
					}
				})
			},
			onCancel() {
				//console.log('Cancel');
			},
		});
	}

	hanChange = info => {
		//console.log(info, '0')
		const status = info.file.status;
		if (status === 'done') {
			// 
			//message.success(`${info.file.name}`);
		} else if (status === 'error') {
			message.error(`${info.file.name}`);
		} else if (status === 'removed') {
			//console.log('delete')
		}
		let fileList = [...info.fileList];
		fileList = JSON.parse(JSON.stringify(fileList));
		fileList = fileList.filter((file) => {
			if (file.status === 'error') {
				return false;
			}
			return true;
		});
		fileList = fileList.map(file => {
			if (file.response) {
				file.url = file.response.body[0].fileURL;
				file.fileId = file.response.body[0].fileId;
				file.id = file.response.body[0].id;
				file.fileName = file.response.body[0].fileName;
				file.filePath = file.response.body[0].fileURL;
			}
			return file;
		});
		fileList = fileList.filter((file) => {
			if (file.response) {
				return file.response.status === 'ok';
			}
			return true;
		});

		this.setState({
			fileList: Object.assign([], [...fileList])
		}, ()=>{
			// 
			let data = this.state.fileList.map((item,index)=>{
				return {
					uid: item.uid,
					name: item.fileName,
					status: 'done',
					url: item.filePath,
					fileId: item.fileId,
					fileName:item.fileName,
					filePath: item.filePath,
					id: item.id
				}
			})

			this.store.epidemicFields.attachments = Object.assign([], data);
		});
	};

	handleCancel = () => this.setState({ previewVisible: false });

	handlePreview = async file => {
		if (!file.url && !file.preview) {
			file.preview = await getBase64(file.originFileObj);
		}
		this.setState({
			previewImage: file.url || file.preview,
			previewVisible: true,
		});
	};

	// 
	handleAttment = (params) => {
		// 
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.store.epidemicFields.attachments = Object.assign([], arr);
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'normalTemperature',
				key: 'normalTemperature',
				align: 'center',
				render: (text) => (
					<span>
                    {text==true?"":""}
                    </span>
				),
			},
			 {
				title: '',
				dataIndex: 'protect',
				key: 'protect',
				align: 'center',
				render: (text) => (
					 <span>
                    {text==true?"":""}
                    </span>
				 ),
			},
			{
				title: '',
				dataIndex: 'disinfection',
				key: 'disinfection',
				align: 'center',
				render: (text) => (
					<span>
                    {text==true?"":""}
                    </span>
				),
			},
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text) => (
					<div>
						{text==null?"":text}
					</div>
				)
			},
			{
				title: '',
				key: 'action',
				render: (text, record) => (
					<span>
			            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
			               
			            </Tag>
			            <Divider type="vertical"/>
			            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
			                 
			              </Tag>
			         </span>
				),
			}
		];
		const fileds = mobx.toJS(this.store.epidemicFields);
		const props = {
			data: {
				type: 1,
				sourceId: this.store.epidemicFields.id // id
			},
			name: 'file',
			multiple: true,
			action:Config.management.multiUpload,
			onChange: this.hanChange,
			fileList: mobx.toJS(this.store.epidemicFields.attachments),
			onPreview: this.handlePreview,
			listType:"picture-card"
		}

		const { previewVisible, previewImage, fileList } = this.state;
		const uploadButton = (
			<div>
				<Icon type="plus" />
				<div className="ant-upload-text"></div>
			</div>
		);

		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" onClick={this.handleAdd}></Button>
				<Table columns={columns}
				       pagination={false}
				       size="middle"
				       className="xlj-table mt25"
				       dataSource={mobx.toJS(this.store.epidemicTableData)}
				/>
				<div style={styles.pagination}>
					<Pagination
						current={this.store.epidemicPageIndex}
						pageSize={this.store.epidemicPageSize}
						total={this.store.epidemicTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showTotal={this.showTotal}
						showQuickJumper/>
				</div>
				<Modal
					title={this.state.title == "add" ? "" : ""}
					visible={this.state.visible}
					width="50%"
					style={{minWidth: '500px'}}
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form>
						<Row className={'clearfix'}>
							<Col span={24}>
								<Form.Item
                                    labelCol={{
                                        xs: {span: 24},
                                        sm: {span: 8},
                                    }}
                                    wrapperCol={{
                                        xs: {span: 24},
                                        sm: {span: 16},
                                    }}
									label={``}>
									{getFieldDecorator(`date`, {
										initialValue: fileds.date == null ? "" : moment(fileds.date, 'YYYY-MM-DD'),
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<DatePicker
											format="YYYY-MM-DD"
											value={fileds.date == null ? "" : moment(fileds.date, 'YYYY-MM-DD')}
											onChange={this.onDateChange.bind(this, 'date')}
										/>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 8},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 16},
									}}
									label={``}
								>
									{getFieldDecorator(`normalTemperature`, {
										initialValue: fileds.normalTemperature == null ? "false" : fileds.normalTemperature,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<Radio.Group onChange={this.handleChage.bind(this, 'normalTemperature')}>
											<Radio value="true"></Radio>
											<Radio value="false"></Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}} className={fileds.normalTemperature == "true" ? "hidden" : ""}>
								<Form.Item
									wrapperCol={{ span: 12, offset: 8 }}
								>
									{getFieldDecorator(`normalTemperatureText`, {
										initialValue: fileds.normalTemperatureText,
										rules: [
											{
												required: fileds.normalTemperature == "true" ? false : true,
												message: '',
											},
										],
									})(
										<TextArea autosize={{minRows: 2}}
										          placeholder=""
										          onChange={this.handleChage.bind(this, "normalTemperatureText")}
										          value={fileds.normalTemperatureText}
										/>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 8},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 16},
									}}
									label={``}
								>
									{getFieldDecorator(`protect`, {
										initialValue: fileds.protect == null ? "false" : fileds.protect,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<Radio.Group onChange={this.handleChage.bind(this, 'protect')}>
											<Radio value="true"></Radio>
											<Radio value="false"></Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}} className={fileds.protect == "true" ? "hidden" : ""}>
								<Form.Item
									wrapperCol={{ span: 12, offset: 8 }}
								>
									{getFieldDecorator(`protectText`, {
										initialValue: fileds.protectText,
										rules: [
											{
												required: fileds.protect == "true" ? false : true,
												message: '',
											},
										],
									})(
										<TextArea autosize={{minRows: 2}}
										          placeholder=""
										          onChange={this.handleChage.bind(this, "protectText")}
										          value={fileds.protectText}
										/>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 8},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 16},
									}}
									label={``}
								>
									{getFieldDecorator(`disinfection`, {
										initialValue: fileds.disinfection == null ? "false" : fileds.disinfection,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<Radio.Group onChange={this.handleChage.bind(this, 'disinfection')}>
											<Radio value="true"></Radio>
											<Radio value="false"></Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}} className={fileds.disinfection == "true" ? "hidden" : ""}>
								<Form.Item
									wrapperCol={{ span: 12, offset: 8 }}
									>
									{getFieldDecorator(`disinfectionText`, {
										initialValue: fileds.disinfectionText,
										rules: [
											{
												required: fileds.disinfection == "true" ? false : true,
												message: '',
											},
										],
									})(
										<TextArea autosize={{minRows: 2}}
										          placeholder=""
										          onChange={this.handleChage.bind(this, "disinfectionText")}
										          value={fileds.disinfectionText}
										/>
									)}
								</Form.Item>
							</Col>
							<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 8},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 16},
									}}
									label={``}>
									<MyUpload
										length={9}
										saveAttachment={this.handleAttment.bind(this)}
										disabled={false}
										multiple={true}
										listType="picture-card"
										ossData={this.props.ossData}
										fileList={mobx.toJS(this.store.epidemicFields.attachments)}
									/>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</Modal>
				<Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
					<img alt="example" style={{ width: '100%' }} src={previewImage} />
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Epidemic);
export default form;






