import React from 'react';
import {observer} from 'mobx-react';
import {
	Row,
	Col,
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	Form,
	Tag, Divider, Icon,
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import {List} from "immutable";
import Config from "@/config";
import MyUpload from '@/components/common/MyUpload';
import AutoSelect from "@/components/common/AutoSelect";
import ProEditTable from './ProEditTable';

import Schemas from "@/utils/Schemas/xmgl";

const confirm = Modal.confirm;

function getBase64(file) {
	return new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = error => reject(error);
	});
}

@observer
class Progress extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			fileList: [],
			previewVisible: false,
			previewImage: '',
		}
	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	getTabeData = () => {
		this.store.getPlanList();
	}

	// 
	handleAdd = () => {
		this.setState({
			visible: true,
			title: 'add'
		}, () => {
			// 
			const param = moment().format("YYYY-MM-DD")
			this.store.getTodayGoals(param).then((res) => {
				this.store.planFields = Object.assign(mobx.toJS(this.store.planFields),
					{
						date: moment().format("YYYY-MM-DD"),
						"goalsTomorrow": [{
							"id": "",
							"projectId": this.store.projectId,
							"text": "",
							"rate": 100,
							"date": null
						}],
						goals: Object.assign([], res == null ? [] : res),
						"dayTasks": []
					});
			});

			// 
			this.getPosList();
			// 
			this.getDayTaskByDate(param);
		});
	}

	getPosList = () => {
		const {date} = this.store.planFields;
		const params = {
			projectId: this.store.projectId,
			beginTime: moment(date).valueOf(),
			endTime: moment(date).valueOf()
		};
		this.store.getPositionList(params)
		.then((res) => {
			if (res) {
				this.store.planFields.positions = Object.assign([], res == null ? [] : res);
			}
		});
	}

	// 
	onDateChange = (type, value, dateString) => {
		let _this = this;
		//
		let fileds = JSON.parse(JSON.stringify(this.store.planFields));
		if (dateString !== fileds.date) {
			_this.store.planFields[type] = dateString;
			// 
			_this.getPosList();
			// 
			_this.getDayTaskByDate(dateString);
			/*confirm({
				title: '?',
				okText: '',
				okType: 'danger',
				cancelText: '',
				onOk() {

				},
				onCancel() {
					//console.log('Cancel');
				},
			});*/
		} else {
			_this.store.planFields[type] = fileds.date;
		}
	}

	// 
	getDayTaskByDate = (date) => {
		this.store.getDayTaskByDate({
			"projectId": this.store.projectId,
			"date": date,
		}).then((res) => {
			this.store.planFields = Object.assign(mobx.toJS(this.store.planFields), {
				dayTasks: Object.assign([], res == null ? [] : res),
			});
		});
	}

	// 
	handleChage = (type, e) => {
		this.store.planFields[type] = e.target.value;
	}

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.planFields = Object.assign({}, {
				"id": null,
				"date": null,
				"attachments": [],
				"goals": [],
				"goalsTomorrow": [{
					"id": "",
					"projectId": "",
					"text": "",
					"rate": 100,
					"date": null,
					"ztaskId": ""
				}],
				"text": "",
				"positions": [],
				"dayTasks": []
			});
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			if (err) {
				return false;
			}

			// 
			let array = JSON.parse(JSON.stringify(this.store.planFields.attachments))
			let arr = [];
			array.length && array.map((item, index) => {
				arr.push({
					fileId: item.fileId,
					fileName: item.fileName,
					filePath: item.filePath,
					//id: item.id
				})
			});

			this.store.savePlan(
				Object.assign(mobx.toJS(this.store.planFields), {
					attachments: arr,
					projectId: this.store.projectId
				})
			).then((res) => {
				if (res) {
					message.success("")
					this.getTabeData();
					this.handleCancle();
				} else {
					message.success(res.message || "")
				}
			});
		})
	}

	showSizeChange = async (current, pageSize) => {
		this.store.planPageIndex = 1;
		this.store.planPageSize = pageSize;
		this.store.getPlanList();
	}

	onPageChange = async (pageNumber) => {
		this.store.planPageIndex = pageNumber;
		this.store.getPlanList();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	// 
	handEdit = async (param) => {
		this.setState({
			visible: true,
			title: 'edit'
		}, () => {
			delete param.key;
			this.store.planFields = Object.assign({}, param, {
				attachments: param.attachments !== null ? param.attachments.map((vitem, index) => {
					return Object.assign({}, {
						uid: vitem.fileId,
						name: vitem.fileName,
						status: 'done',
						url: vitem.filePath,
						fileId: vitem.fileId,
						fileName: vitem.fileName,
						filePath: vitem.filePath,
						id: vitem.fileId
					})
				}) : [],
				positions: param.positions == null ? [] : param.positions
			});
			// 
			this.getPosList();
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delPlan(param.id).then((res) => {
					if (res == true) {
						message.success("");
						_this.store.planPageIndex = 1;
						_this.store.planPageSize = 10;
						_this.store.getPlanList();
					} else {
						message.error('');
					}
				})
			},
			onCancel() {
				//console.log('Cancel');
			},
		});
	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		//console.log(value);
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	// 
	remove = (index) => {
		let customList = JSON.parse(JSON.stringify(this.store.planFields.goalsTomorrow));
		let list = List(customList);
		this.store.planFields.goalsTomorrow = Object.assign([], list.delete(index).toJS())
		this.props.form.resetFields();
	};

	// 
	add = () => {
		let customList = JSON.parse(JSON.stringify(this.store.planFields.goalsTomorrow));
		this.store.planFields.goalsTomorrow = [...customList, {
			"id": "",
			"projectId": this.store.projectId,
			"text": "",
			"rate": 100,
			"date": null,
			"ztaskId": ""
		}]
		this.props.form.resetFields();
	};

	// input
	doChange = (type, index, value, e) => {
		this.props.form.resetFields();
		const {val} = e.props;
		let customList = JSON.parse(JSON.stringify(this.store.planFields.goalsTomorrow));
		let list = List(customList);
		if (val == undefined) {  // 
			list.update(index, (val) => {
				val.text = value;
				val.ztaskId = "";
				return val;
			});
		} else {  // 
			list.update(index, (val) => {
				val.text = value;
				val.ztaskId = val.id;
				return val;
			});
		}
		this.store.planFields.goalsTomorrow = Object.assign([], list.toJS())
	}

	// 
	hanleNumber = (type, index, value) => {
		let customList = JSON.parse(JSON.stringify(this.store.planFields.goals));
		let list = List(customList);
		list.update(index, (val) => {
			val.rate = value;
			return val;
		});
		//console.log(list.toJS(), customList, '9',value)
		this.store.planFields.goals = Object.assign([], list.toJS())
	}

	handleChange = info => {
		//console.log(info, '0')
		const status = info.file.status;
		if (status === 'done') {
			// 
			//message.success(`${info.file.name}`);
		} else if (status === 'error') {
			message.error(`${info.file.name}`);
		} else if (status === 'removed') {
			//console.log('delete')
		}
		let fileList = [...info.fileList];
		fileList = JSON.parse(JSON.stringify(fileList));
		fileList = fileList.filter((file) => {
			if (file.status === 'error') {
				return false;
			}
			return true;
		});
		fileList = fileList.map(file => {
			if (file.response) {
				file.url = file.response.body[0].fileURL;
				file.fileId = file.response.body[0].fileId;
				file.id = file.response.body[0].fileId;
				file.fileName = file.response.body[0].fileName;
				file.filePath = file.response.body[0].fileURL;
			}
			return file;
		});
		fileList = fileList.filter((file) => {
			if (file.response) {
				return file.response.status === 'ok';
			}
			return true;
		});

		this.setState({
			fileList: Object.assign([], [...fileList])
		}, () => {
			// 
			let data = this.state.fileList.map((item, index) => {
				return {
					uid: item.uid,
					name: item.fileName,
					status: 'done',
					url: item.filePath,
					fileId: item.fileId,
					fileName: item.fileName,
					filePath: item.filePath,
					id: item.fileId
				}
			})

			this.store.planFields.attachments = Object.assign([], data);
		});
	};

	handleCancel = () => this.setState({previewVisible: false});

	handlePreview = async file => {
		if (!file.url && !file.preview) {
			file.preview = await getBase64(file.originFileObj);
		}
		this.setState({
			previewImage: file.url || file.preview,
			previewVisible: true,
		});
	};

	// 
	saveAttachment = (pitem, index, params) => {
		// 
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				positionId: pitem.id,
				createTime: pitem.createTime,
				dateTime: moment(this.store.planFields.date).valueOf(),
				id: item.id
			})
		});
		const positions = JSON.parse(JSON.stringify(this.store.planFields.positions));
		positions[index].attachments = Object.assign([], arr);
		this.store.planFields['positions'] = Object.assign([], positions);
	}

	// 
	handleAttment = (params) => {
		// 
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.store.planFields.attachments = Object.assign([], arr);
	}

	updateData = (type, dataSource) => {
		this.store.planFields[type] = Object.assign([], dataSource);
	}

	// 
	handleSend = (param) => {
		let _this = this;
		if (param.notifyStatus == "1") {
			confirm({
				title: '?',
				okText: '',
				okType: 'danger',
				cancelText: '',
				onOk() {
					_this.store.sendNotifys(param.id).then((res) => {
						if (res == true) {
							message.success("");
						} else {
							message.error("");
						}
					})
				},
				onCancel() {

				},
			});
		} else {
			confirm({
				title: '?',
				okText: '',
				okType: 'danger',
				cancelText: '',
				onOk() {
					_this.store.sendNotifys(param.id).then((res) => {
						if (res == true) {
							message.success("");
						} else {
							message.error("");
						}
					})
				},
				onCancel() {

				},
			});
		}
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
				width: '50px'
			},
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text, record) => (
					<span>
            {text + ""}
          </span>
				),
				width: '250px'
			},
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text, record) => (
					<div>
						{text == null ? "" : text}
					</div>
				),
				width: '150px'
			},/* {
				title: '',
				dataIndex: 'text',
				key: 'text',
				align: 'left',
			},*/
			{
				title: '',
				key: 'action',
				fixed: 'right',
				align: 'left',
				width: 200,
				render: (text, record) => (
					<span>
			            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
			               
			            </Tag>
			            <Divider type="vertical"/>
			            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
			                 
			            </Tag>
			            <Divider type="vertical"/>
			            <Tag className="pointer" color={'blue'} onClick={this.handleSend.bind(this, record)}>
			                
			            </Tag>
                    </span>
				),
			}
		];
		const fileds = mobx.toJS(this.store.planFields);
		const formItems = mobx.toJS(this.store.planFields.goalsTomorrow).length && mobx.toJS(this.store.planFields.goalsTomorrow).map((item, index) => {
			return (<div className="pr" style={{width: '80%'}}
			             key={'goals_tomorrow' + index}>
					<AutoSelect
						url={Config.xmgl.getZplanTaskList}
						query={Schemas.xmgl.getZplanTaskList}
						variables={{
							projectId: this.store.projectId,
							beginTime: moment(this.store.planFields.date).subtract(1, "years").valueOf(), //3,
							endTime: moment(this.store.planFields.date).add(1, "years").valueOf(), //3,
						}}
						onChange={this.doChange.bind(this, "text", index)}
						value={item.text}
						type="getZplanTaskList"
					/>
					{/*<TextArea autosize={{minRows: 2}}
					          placeholder=""
					          style={{width: '100%', display: 'inlineBlock'}}
					          value={item.text}
					          onChange={this.doChange.bind(this, "text", index)}
					/>*/}
					{index >= 1 ? <Button
						shape="circle"
						className="dynamic-delete-button"
						onClick={this.remove.bind(this, index)}
						size="small"
						icon="minus"
						style={{top: '6px'}}
						type="default"/> : null}
				</div>
			)
		});
		const props = {
			data: {
				type: 1,
				sourceId: this.store.planFields.id // id
			},
			name: 'file',
			multiple: true,
			action: Config.management.multiUpload,
			onChange: this.handleChange,
			fileList: mobx.toJS(this.store.planFields.attachments),
			onPreview: this.handlePreview,
			listType: "picture-card"
		}

		const {previewVisible, previewImage, fileList} = this.state;
		const uploadButton = (
			<div>
				<Icon type="plus"/>
				<div className="ant-upload-text"></div>
			</div>
		);

		return (
			<div className='w' style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" onClick={this.handleAdd}></Button>
				<Table columns={columns}
				       pagination={false}
				       size="middle"
				       className="xlj-table mt25"
				       dataSource={mobx.toJS(this.store.planTableData)}
				/>
				<div style={styles.pagination}>
					<Pagination
						current={this.store.planPageIndex}
						pageSize={this.store.planPageSize}
						total={this.store.planTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showTotal={this.showTotal}
						showQuickJumper/>
				</div>
				<Modal
					title={this.state.title == "add" ? "" : ""}
					visible={this.state.visible}
					width={'80%'}
					style={{minWidth: '880px'}}
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form>
						<Row className={'clearfix'}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 6},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 18},
									}}
									label={``}>
									<div style={{fontWeight: 400}}>{moment(fileds.date).format("YYYYMMDD")}</div>
								</Form.Item>
							</Col>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 6},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 18},
									}}
									label={``}>
									{getFieldDecorator(`date`, {
										initialValue: fileds.date == null ? "" : moment(fileds.date, 'YYYY-MM-DD'),
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<DatePicker
											format="YYYY-MM-DD"
											value={fileds.date == null ? "" : moment(fileds.date, 'YYYY-MM-DD')}
											onChange={this.onDateChange.bind(this, 'date')}
										/>
									)}
								</Form.Item>
							</Col>
							{/*<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 6},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 18},
									}}
									label={``}>
									<TextArea autosize={{minRows: 2}}
									          style={{width: '80%'}}
									          placeholder=""
									          onChange={this.handleChage.bind(this, "text")}
									          value={fileds.text}
									/>
								</Form.Item>
							</Col>*/}
							<Col span={24} style={{marginTop: '8px'}}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 6},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 18},
									}}
									label={``}>
									<MyUpload
										length={1}
										saveAttachment={this.handleAttment.bind(this)}
										disabled={false}
										multiple={false}
										listType="picture-card"
										ossData={this.props.ossData}
										fileList={mobx.toJS(this.store.planFields.attachments)}
									/>
								</Form.Item>
							</Col>
							{fileds.positions.map((item, index) => {
								return (<Col key={'pos' + index} span={24} style={{marginTop: '8px'}}>
									<Form.Item
										labelCol={{
											xs: {span: 24},
											sm: {span: 6},
										}}
										wrapperCol={{
											xs: {span: 24},
											sm: {span: 18},
										}}
										label={`${item.name}`}>
										<MyUpload
											key={index}
											length={1}
											saveAttachment={this.saveAttachment.bind(this, item, index)}
											disabled={false}
											multiple={false}
											listType="picture-card"
											ossData={this.props.ossData}
											fileList={item.attachments == null ? [] : item.attachments.map((vitem) => {
												return Object.assign({}, {
													uid: vitem.fileId,
													name: vitem.fileName,
													status: 'done',
													url: vitem.filePath,
													fileId: vitem.fileId,
													fileName: vitem.fileName,
													filePath: vitem.filePath,
													id: vitem.id,
													positionId: vitem.positionId,
													createTime: vitem.createTime,
													dateTime: vitem.dateTime,
												})
											})}
										/>
									</Form.Item>
								</Col>)
							})}
						</Row>
						{/*<Row className={'clearfix'}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										md: {span: 6},
										sm: {span: 24},
									}}
									wrapperCol={{
										xs: {span: 24},
										md: {span: 18},
										sm: {span: 24},
									}}
									label={``}
								>
									{mobx.toJS(fileds.goals).length>0?mobx.toJS(fileds.goals).map((item, index) => {
										return (<div key={'goals-' + index}>
											<Input disabled={true}
											       value={item.text}
											       style={{width: '60%', display: 'inlineBlock'}}/>
											<InputNumber
												style={{marginLeft: '10px', width: '80px', display: 'inlineBlock'}}
												min={0}
												max={100}
												step={10}
												placeholder=""
												value={item.rate}
												onChange={this.hanleNumber.bind(this, "rate", index)}
												formatter={this.limitDecimals}
												parser={this.limitDecimals}/>
											<span className="ml10">%</span></div>)
									}):""}
								</Form.Item>
							</Col>
						</Row>
						<Row className={'clearfix'}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 6},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 18},
									}}
									label={``}>
									{formItems ? formItems : null}
									<Button type="primary"
									        block
									        style={{width: '80%'}}
									        onClick={this.add}>
										<Icon type="plus"/> 
									</Button>
								</Form.Item>
							</Col>
						</Row>*/}
					</Form>
					{/**/}
					<ProEditTable
						planId={this.store.planFields.id}
						dataSource={fileds.dayTasks}
						updateData={this.updateData}
						projectId={this.store.projectId}
						date={fileds.date == null ? "" : fileds.date}
					/>
				</Modal>
				<Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
					<img alt="example" style={{width: '100%'}} src={previewImage}/>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Progress);
export default form;






