import React from 'react';
import {Select} from 'antd';
import PropTypes from 'prop-types';
const Option = Select.Option;

export default class AsyncSelectMuti extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      value: props.value || [],
    };
  }

  componentWillReceiveProps(nextProps) {
    if ('value' in nextProps && this.props.value !== nextProps.value) {
      this.setState({
        value: nextProps.value
      });
    }
  }

  componentDidMount() {
    if (!this.props.url) {
      return;
    }
    this.handleFocus();
  }

  handleChange = (value) => {
    if (value === undefined) return false;
    this.setState({value: value === undefined?"":value});
    let {onChange} = this.props;
    if (onChange) {
      onChange(value);
    }
  }

  handleFocus = () => {
    fetch(this.props.url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        query: this.props.query,
        variables: {"appId" :"bocspace","code":"bocspace.group","pageIndex":1,"pageSize":100}
      })
    })
    .then(response => response.json())
    .then(json => {
      let data = json.data.getUserInfoListByRoleCode.body;
      if (this.props.dataMapFunc) {
        data = data.map(item => this.props.dataMapFunc(item));
      }
      this.setState({
        data
      });
    });
  }

  render() {
    const options = this.state.data.map((d,i)=> <Option value={d.id} key={i}>{d.realname}</Option>);

    return (
      <Select
        value={this.props.value}
        placeholder={this.props.placeholder}
        style={this.props.style}
        onChange={this.handleChange}
        onFocus={this.handleFocus}
        onBlur={this.props.onBlur}
        disabled={this.props.disabled}
        mode="multiple"
      >
        {options}
      </Select>
    );
  }
}

AsyncSelectMuti.propTypes = {
  style: PropTypes.object,
  placeholder: PropTypes.string,
  value: PropTypes.string, // 
  url: PropTypes.string,
  dataMapFunc: PropTypes.func,
  onChange: PropTypes.func,
};



