import React from 'react';
import {observer} from 'mobx-react';
import {
	Row,
	Col,
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	InputNumber,
	Select,
	Form,
	Tag, Divider, AutoComplete
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import Config from '@/config';
import Schemas from "@/utils/Schemas/xmgl";
import AutoSelect from "@/components/common/AutoSelect";

const {Option} = Select;
const confirm = Modal.confirm;


@observer
class Person extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			filteredInfo: null,
			sortedInfo: null,
			workerType: [
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
				{text: '', value: ''},
			],
		}
	}

	componentDidMount() {
		//this.getTabeData();
		this.props.onRef(this);
	}

	componentWillUnmount() {
		//clearTimeout(myClear)
	};

	getTabeData = () => {
		this.store.getAllPersonByProjectIdAndType();
	}

	// 
	handleAdd = () => {
		this.setState({visible: true, title: 'add'}, () => {
			this.store.personFields = Object.assign(mobx.toJS(this.store.personFields),
				{
					averageHours: 8,
					time: moment().format("YYYY-MM-DD"),
					startTime: "08:00:00",
					endTime: "18:00:00"
				})
		});
	}

	handleChage = (type, e) => {
		this.store.personFields[type] = e.target.value;
	}

	handleChangeTable = (pagination, filters, sorter) => {
		//console.log('Various parameters', pagination, filters, sorter);
		this.setState({
			filteredInfo: filters,
			sortedInfo: sorter,
		});
	};

	clearFilters = () => {
		this.setState({filteredInfo: null});
	};

	handleInputNumberChage = (type, number) => {
		this.store.personFields[type] = number;
	}

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.personFields = Object.assign({}, {
				id: null,
				projectId: '1',
				date: null,
				dateEnd: null,
				count: 0,
				type: "",
				averageHours: 8,
				// remark: '',
				time: moment().format("YYYY-MM-DD"),
				startTime: "08:00:00",
				endTime: "18:00:00",
				"taskId": '',
				"expectResourceId": "",
				"resourceType": "",
				"dayTaskName": "",
			});
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			//console.log(fieldsValue);
			if (err) {
				return false;
			}

			let personFields = mobx.toJS(this.store.personFields);
			const date = personFields.time + " " + personFields.startTime + ":00";
			const dateEnd = personFields.time + " " + personFields.endTime + ":00";

			delete personFields.time;
			delete personFields.startTime;
			delete personFields.endTime;

			this.store.personFields = Object.assign(personFields,
				{
					date,
					dateEnd
				}
			);
			this.store.savePerson().then((res) => {
				if (res) {
					message.success("")
					this.handleCancle();
					this.getTabeData();
				} else {
					message.success(res.message || "")
				}
			});
		})
	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	showSizeChange = async (current, pageSize) => {
		this.store.personPageIndex = 1;
		this.store.personPageSize = pageSize;
		this.store.getAllPersonByProjectIdAndType();
	}

	onPageChange = async (pageNumber) => {
		this.store.personPageIndex = pageNumber;
		this.store.getAllPersonByProjectIdAndType();
	}

	onChangeSelect = (value) => {
		//console.log(value, 'change')
		this.store.personFields['type'] = value;
	}

	onBlur = (val) => {
		console.log('blur:', val);
	}

	onFocus = () => {
		console.log('focus')
	}

	onSearch = (val) => {
		console.log('search:', val);
	}

	onSelect = (val) => {
		console.log('select:', val);
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	// 
	handEdit = async (param) => {
		this.setState({
			visible: true,
			title: 'edit'
		}, () => {

			delete param.key;
			delete param.selested;

			// 
			let date = param.date;
			let dateEnd = param.dateEnd;
			const time = param.date == null ? moment().format("YYYY-MM-DD") : date.split(" ").length && date.split(" ")[0];
			const startTime = param.date == null ? null : date.split(" ").length && date.split(" ")[1];
			const endTime = param.dateEnd == null ? null : dateEnd.split(" ").length && dateEnd.split(" ")[1];

			this.store.personFields = Object.assign(mobx.toJS(this.store.personFields), param, {
				time,
				startTime,
				endTime
			})
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delPerson(param.id).then((res) => {
					if (res == true) {
						message.success("");
						_this.store.personPageIndex = 1;
						_this.store.personPageSize = 10;
						_this.store.getAllPersonByProjectIdAndType();
					} else {
						message.error('');
					}
				})
			},
			onCancel() {
				//console.log('Cancel');
			},
		});
	}

	// 
	onDateChange = (type, value, dateString) => {
		//console.log(type, value, dateString, '')
		this.store.personFields[type] = dateString;
	}

	// 
	handDate = (type, time, timeString) => {
		//console.log(type, time, timeString, '')
		this.store.personFields[type] = timeString;
	}

	handleSelChange = () => {

	}

	doChange = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.personFields.taskId = "";
			this.store.personFields.dayTaskName = "";
			this.store.expectId = "";
		} else {  // 
			this.store.personFields.taskId = val.id
			this.store.personFields.dayTaskName = val.name;
			this.store.expectId = val.expectId;
		}
	}

	doChange2 = (value, e) => {
		const {val} = e.props;
		if (val == undefined) {  // 
			this.store.personFields.resourceType = "";
			this.store.personFields.expectResourceId = "";
		} else {  // 
			this.store.personFields.resourceType = val.type;
			this.store.personFields.expectResourceId = val.id;
		}
	}

	render() {
		let {sortedInfo, filteredInfo, workerType} = this.state;
		sortedInfo = sortedInfo || {};
		filteredInfo = filteredInfo || {};

		const {getFieldDecorator} = this.props.form;
		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		}


		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'date',
				key: 'date',
				align: 'center',
				render: (text, record) => (
					<div>
						{text == null ? "" : text}
					</div>
				)
			},
			{
				title: '',
				dataIndex: 'dateEnd',
				key: 'dateEnd',
				align: 'center',
				render: (text, record) => (
					<div>
						{text == null ? "" : text}
					</div>
				)
			},
			{
				title: '',
				dataIndex: 'count',
				key: 'count',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'type',
				key: 'type',
				align: 'center',
				filters: workerType,
				filteredValue: filteredInfo.type || null,
				onFilter: (value, record) => record.type.includes(value),
			}, {
				title: '',
				dataIndex: 'averageHours',
				key: 'averageHours',
				align: 'center',
			},
			// {
			// 	title: '',
			// 	dataIndex: 'remark',
			// 	key: 'remark',
			// },
			{
				title: '',
				key: 'action',
				render: (text, record) => (
					<span>
            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
               
            </Tag>
            <Divider type="vertical"/>
            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
                 
              </Tag>
          </span>
				),
			}
		];
		const fileds = mobx.toJS(this.store.personFields);
		const options = workerType.map((group, index) => (
			<Option key={index} value={group.text}>
				{group.value}
			</Option>
		))

		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Button type="primary" onClick={this.handleAdd}></Button>
				<Table columns={columns}
				       pagination={false}
				       size="middle"
				       className="xlj-table mt25"
				       dataSource={mobx.toJS(this.store.personTableData)}
				       onChange={this.handleChangeTable}
				/>
				<div style={styles.pagination}>
					<Pagination
						current={this.store.personPageIndex}
						pageSize={this.store.personPageSize}
						//pageSizeOptions={["20", "50", "100", "500"]}
						total={this.store.personTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showTotal={this.showTotal}
						showQuickJumper/>
				</div>
				<Modal
					title={this.state.title == "add" ? "" : ""}
					visible={this.state.visible}
					width="50%"
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form {...formItemLayout}>
						<Row className={'clearfix'}>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('dayTaskName', {
										initialValue: fileds.dayTaskName,
										rules: [{required: true, message: ''}],
									})(
										<AutoSelect
											url={Config.xmgl.getDayTaskByDate}
											query={Schemas.xmgl.getDayTaskByDate}
											variables={{
												projectId: this.store.projectId,
												data: fileds.time == null ? "" : fileds.time
											}}
											onChange={this.doChange}
											value={fileds.dayTaskName == null || fileds.dayTaskName == "" ? "" : fileds.dayTaskName}
											type="getDayTaskByDate"
										/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('expectResourceId', {
										initialValue: fileds.expectResourceId,
										rules: [{required: true, message: ''}],
									})(<AutoSelect
										url={Config.xmgl.getExpectResourceList}
										query={Schemas.xmgl.getExpectResourceList}
										variables={{
											expectId: this.store.expectId,
											type: ""
										}}
										onChange={this.doChange2}
										value={fileds.expectResourceId == null || fileds.expectResourceId == "" ? "" : fileds.expectResourceId}
										type="getExpectResourceList"
										keyId={"id"}
									/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label="">
									{getFieldDecorator('time', {
										initialValue: fileds.time == null ? "" : moment(fileds.time, 'YYYY-MM-DD'),
										rules: [{required: true, message: ''}],
									})(<DatePicker
										dateFormat={'YYYY-MM-DD'}
										onChange={this.onDateChange.bind(this, 'time')}
										style={{width: '100%'}}/>)}
								</Form.Item>
								{/*<Form.Item label={``}>
									{getFieldDecorator(`time`, {
										initialValue: fileds.date==null?[]:[moment(fileds.date, 'YYYY-MM-DD HH:mm:ss'), moment(fileds.dateEnd, 'YYYY-MM-DD HH:mm:ss')],
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(<div className="w">
										<RangePicker
												value={ fileds.date==null?[]:[moment(fileds.date, 'YYYY-MM-DD HH:mm:ss'), moment(fileds.dateEnd, 'YYYY-MM-DD HH:mm:ss')]}
											showTime={{
												format: "HH:mm:ss",
												//defaultValue: [moment('08:00:00', 'HH:mm:ss'), moment('18:59:59', 'HH:mm:ss')],
											}}
											style={{width: '100%'}}
											format="YYYY-MM-DD HH:mm:ss"
											onChange={this.ondateChange}
											onOk={this.onOk}
											/>
										</div>
									)}
								</Form.Item>*/}
							</Col>
							{/* <Col span={12}>
								<Form.Item label=""
								           labelCol={{
									           xs: {span: 24},
									           sm: {span: 8},
								           }}
								           wrapperCol={{
									           xs: {span: 24},
									           sm: {span: 16},
								           }}
								           style={{marginBottom: 0}}>
									<div>
										<Form.Item
											style={{display: 'inline-block', width: 'calc(50% - 12px)'}}
										>{getFieldDecorator('startTime', {
											initialValue: fileds.startTime == null ? "" : moment(fileds.startTime, 'HH:mm'),
											rules: [{required: true, message: ''}],
										})(
											<TimePicker
												style={{width: '100%'}}
												format={'HH:mm'}
												value={fileds.startTime == null ? "" : moment(fileds.startTime, 'HH:mm')}
												onChange={this.handDate.bind(this, 'startTime')}
												placeholder=""
											/>
										)}
										</Form.Item>
										<span style={{display: 'inline-block', width: '24px', textAlign: 'center'}}>-</span>
										<Form.Item
											style={{display: 'inline-block', width: 'calc(50% - 12px)'}}
										>
											{getFieldDecorator('endTime', {
												initialValue: fileds.endTime == null ? "" : moment(fileds.endTime, 'HH:mm'),
												rules: [{required: true, message: ''}],
											})(
												<TimePicker
													style={{width: '100%'}}
													format={'HH:mm'}
													value={fileds.endTime == null ? "" : moment(fileds.endTime, 'HH:mm')}
													onChange={this.handDate.bind(this, 'endTime')}
													placeholder=""
												/>
											)}
										</Form.Item>
									</div>
								</Form.Item>
							</Col> */}
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('count', {
										initialValue: fileds.count,
										rules: [{required: true, message: ''}],
									})(<InputNumber
										style={{width: '100%'}}
										min={0}
										onChange={this.handleInputNumberChage.bind(this, "count")}
										formatter={this.limitDecimals}
										parser={this.limitDecimals}/>)}
								</Form.Item>
							</Col>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('type', {
										initialValue: fileds.type,
										rules: [{required: true, message: ''}],
									})(
										<AutoComplete
											dataSource={options}
											style={{width: "100%"}}
											onSelect={this.onSelect}
											onSearch={this.onSearch}
											onChange={this.onChangeSelect}
											filterOption={(input, option) =>
												option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
											}
											placeholder=""
										/>
									)}
								</Form.Item>
								{/*<Select
											showSearch
											placeholder=""
											style={{width: "100%"}}
											optionFilterProp="children"
											onChange={this.onChangeSelect}
											onBlur={() => {
												return this.state.newState ? this.onChangeSelect : null
											}}
											value={this.state.newState}
											onSearch={this.onSearch}
											filterOption={(input, option) =>
												option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
											}
										>
											{workerType.map(item => {
												return (<Option value={item.value}>{item.text}</Option>)
											})}
										</Select>*/}
							</Col>
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('averageHours', {
										initialValue: fileds.averageHours,
										rules: [{required: true, message: ''}],
									})(<InputNumber
										style={{width: '100%'}}
										min={0}
										onChange={this.handleInputNumberChage.bind(this, "averageHours")}
										formatter={this.limitDecimals}
										parser={this.limitDecimals}/>)}
								</Form.Item>
							</Col>
						</Row>
						{/* <Row className={'clearfix'} style={{marginTop: 8}}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 4},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 20},
									}}
									label={``}>
									<TextArea rows={4}
									          placeholder=""
									          onChange={this.handleChage.bind(this, "remark")}
									          value={fileds.remark}
									/>
								</Form.Item>
							</Col>
						</Row> */}
					</Form>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Person);
export default form;






