import React from 'react';
import {observer} from 'mobx-react';
import {
	Tabs,
	Row,
	Col,
	Radio,
	Table,
	Pagination,
	message,
	Modal,
	DatePicker,
	Button,
	Input,
	Form,
	Tag, Divider
} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';

const RadioGroup = Radio.Group;
const {TextArea} = Input;
const { TabPane } = Tabs;
const confirm = Modal.confirm;


@observer
class Quality extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			visible: false,
			type: ['', ''],//
			emergencyQuality: ['', '', '', '', ''],//
			emergencySafe: ['', '', ''],//
			state: ['', ''],//
			filteredInfo: null,
			sortedInfo: null,
			title: '', // 
		}
	}

	componentDidMount() {
		this.store.qualityType = 1;
		//this.getTabeData();
		this.props.onRef(this);
	}

	getTabeData = () => {
		this.store.getAllQualityByProjectIdAndType();
	}

	handleAdd = () => {
		const title = this.store.qualityType=='1'?"":"";
		this.setState({visible: true, title: title}, () => {
			this.store.qualityFields = Object.assign(mobx.toJS(this.store.qualityFields),
				{createTime: moment().format("YYYY-MM-DD"), type: this.store.qualityType})
		});
	}

	onDateChange = (type, value, dateString) => {
		this.store.qualityFields[type] = dateString;
	}

	handleChage = (type, e) => {
		this.store.qualityFields[type] = e.target.value;
	}

	handleChangeTable = (pagination, filters, sorter) => {
		this.setState({
			filteredInfo: filters,
			sortedInfo: sorter,
		});
	};

	clearFilters = () => {
		this.setState({filteredInfo: null});
	};

	// 
	handleCancle = () => {
		this.setState({
			visible: false,
		}, () => {
			this.store.qualityFields = Object.assign({}, {
				"id": null,
				"question": "",
				"type": 1, //1 2
				"emergencyQuality": "",  //123
				"createTime": moment().format("YYYY-MM-DD"),
				"state": "",
				"solveTime": null,
				"handler": "",
				"reviewer": "",
				"remark": "",
				planSolveTime:null
			});
		});
		this.props.form.resetFields();
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, fieldsValue) => {
			console.log(fieldsValue);
			if (err) {
				return false;
			}
			this.store.saveQuality().then((res) => {
				if (res) {
					message.success("")
					this.getTabeData();
					this.handleCancle();
				} else {
					message.success(res.message || "")
				}
			});
		})
	}

	// 
	limitDecimals = (value) => {
		const reg = /^(\-)*(\d+)\.(\d\d).*$/;
		//console.log(value);
		if (typeof value === 'string') {
			return !isNaN(Number(value)) ? value.replace(reg, '$1$2.$3') : ''
		} else if (typeof value === 'number') {
			return !isNaN(value) ? String(value).replace(reg, '$1$2.$3') : ''
		} else {
			return ''
		}
	};

	showSizeChange = async (current, pageSize) => {
		this.store.qualityPageIndex = 1;
		this.store.qualityPageSize = pageSize;
		this.store.getAllQualityByProjectIdAndType();
	}

	onPageChange = async (pageNumber) => {
		this.store.qualityPageIndex = pageNumber;
		this.store.getAllQualityByProjectIdAndType();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	callback = (key) => {
		this.store.qualityType = Number(key);
		this.getTabeData();
	}

	// 
	handEdit = async (param) => {
		const title = this.store.qualityType=='1'?"":"";
		this.setState({
			visible: true,
			title
		}, () => {
			delete param.key;
			delete param.selested;
			if (this.store.qualityType=='1') {  // 
				this.store.qualityFields = Object.assign(mobx.toJS(this.store.qualityFields),param, {
					"emergencyQuality": param.emergency,
				})
			} else{
				this.store.qualityFields = Object.assign(mobx.toJS(this.store.qualityFields),param, {
					"emergencySafe": param.emergency,
				})
			}
		})
	}

	// 
	handleDel = (param) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				_this.store.delQuality(param.id).then((res)=>{
					if (res == true) {
						message.success("");
						_this.store.qualityPageIndex = 1;
						_this.store.qualityPageSize = 10;
						_this.store.getAllQualityByProjectIdAndType();
					} else {
						message.error('');
					}
				})
			},
			onCancel() {
				//console.log('Cancel');
			},
		});
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		let {type, emergencyQuality, emergencySafe, state, sortedInfo, filteredInfo} = this.state;

		sortedInfo = sortedInfo || {};
		filteredInfo = filteredInfo || {};

		const styles = {
			pagination: {
				textAlign: 'end',
				marginTop: '20px',
			}
		};
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 10},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 12},
			},
		};

		const columns = [
			{
				title: '',
				dataIndex: 'key',
				key: 'key',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'question',
				key: 'question',
			},
			{
				title: '',
				dataIndex: 'state',
				key: 'state',
				align: 'center',
			},
			{
				title: '',
				dataIndex: 'emergency',
				key: 'emergency',
				align: 'center',
			},
			/*{
				title: '',
				dataIndex: 'type',
				key: 'type',
				filters: [{text: '', value: ''}, {text: '', value: ''}],
				filteredValue: filteredInfo.type || null,
				onFilter: (value, record) => record.type.includes(value),
			},*/
			 {
				title: '',
				dataIndex: 'createTime',
				key: 'createTime',
				 align: 'center',
				 render: (text, record) => (
					 <div>
						 {text==null?"":text}
					 </div>
				 )
			},
			{
				title: '',
				dataIndex: 'solveTime',
				key: 'solveTime',
				align: 'center',
				render: (text, record) => (
					<div>
						{record.state===""?record.planSolveTime:record.solveTime}
					</div>
				)
			},
			// {
			// 	title: '',
			// 	dataIndex: 'handler',
			// 	key: 'handler',
			// },
			{
				title: '',
				dataIndex: 'reviewer',
				key: 'reviewer',
				align: 'center',
			},
			{
				title: '',
				key: 'action',
				render: (text, record) => (
					<span>
            <Tag color={'green'} className="pointer" onClick={this.handEdit.bind(this, record)}>
               
            </Tag>
            <Divider type="vertical"/>
            <Tag color={'red'} className="pointer" onClick={this.handleDel.bind(this, record)}>
                 
              </Tag>
          </span>
				),
			}
			/*{
				title: '',
				dataIndex: 'remark',
				key: 'remark',
			}*/
		];

		const fileds = mobx.toJS(this.store.qualityFields);

		return (
			<div style={{padding: '0 15px 15px 0'}}>
				<Tabs defaultActiveKey="1"
				      className="quality-tabs"
				      onChange={this.callback}>
					<TabPane tab="" key="1">
						<Button type="primary" onClick={this.handleAdd}></Button>
						<Table columns={columns}
						       pagination={false}
						       size="middle"
						       className="xlj-table mt25"
						       dataSource={mobx.toJS(this.store.qualityTableData)}
						       onChange={this.handleChangeTable}
						/>
						<div style={styles.pagination}>
							<Pagination
								current={this.store.qualityPageIndex}
								pageSize={this.store.qualityPageSize}
								total={this.store.qualityTotalCount}
								onShowSizeChange={this.showSizeChange}
								onChange={this.onPageChange}
								showSizeChanger
								showTotal={this.showTotal}
								showQuickJumper/>
						</div>
					</TabPane>
					<TabPane tab="" key="2">
						<Button type="primary" onClick={this.handleAdd}></Button>
						<Table columns={columns}
						       pagination={false}
						       size="middle"
						       className="xlj-table mt25"
						       dataSource={mobx.toJS(this.store.qualityTableData)}
						       onChange={this.handleChangeTable}
						/>
						<div style={styles.pagination}>
							<Pagination
								current={this.store.qualityPageIndex}
								pageSize={this.store.qualityPageSize}
								total={this.store.qualityTotalCount}
								onShowSizeChange={this.showSizeChange}
								onChange={this.onPageChange}
								showSizeChanger
								showTotal={this.showTotal}
								showQuickJumper/>
						</div>
					</TabPane>
				</Tabs>
				<Modal
					title={this.state.title}
					visible={this.state.visible}
					width="50%"
					onOk={this.handleSubmit}
					onCancel={this.handleCancle}
					maskClosable={false}
				>
					<Form {...formItemLayout}>
						<Row className={'clearfix'}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 5},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 19},
									}}
									label={``}>
									{getFieldDecorator(`question`, {
										initialValue: fileds.question,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(<TextArea autosize={{ minRows: 2}}
									             disabled={this.props.title == "edit"}
									             onChange={this.handleChage.bind(this, "question")}
									             value={fileds.question}
										/>
									)}
								</Form.Item>
							</Col>
							{/*<Col span={12}>
										<Form.Item label={``}>
											{getFieldDecorator(`type`, {
												initialValue: fileds.type,
												rules: [
													{
														required: true,
														message: '',
													},
												],
											})(
												<div className="w">
													<RadioGroup onChange={this.handleChage.bind(this, "type")}
													            disabled={this.props.title == "edit"}
													            value={fileds.type}>
														{type.map((item, index) => {
															return (<Radio key={'type' + index} value={item}>{item}</Radio>)
														})}
													</RadioGroup>
												</div>
											)}
										</Form.Item>
									</Col>*/}
						</Row>
						<Row>
							<Col span={24} className="mb5">
								<Form.Item
								labelCol={{
									xs: {span: 24},
									sm: {span: 5},
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 19},
								}}
								label={``}>
									{this.store.qualityType==1?getFieldDecorator(`emergency`, {
										initialValue: fileds.emergencyQuality,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<div className="w">
											<RadioGroup onChange={this.handleChage.bind(this, "emergencyQuality")}
											            disabled={this.props.title == "edit"}
											            value={fileds.emergencyQuality}>
												{emergencyQuality.map((item, index) => {
													return (<Radio key={'emergencyQuality' + index} value={item}>{item}</Radio>)
												})}
											</RadioGroup>
										</div>
									):getFieldDecorator(`emergency`, {
										initialValue: fileds.emergencySafe,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<div className="w">
											<RadioGroup onChange={this.handleChage.bind(this, "emergencySafe")}
											            disabled={this.props.title == "edit"}
											            value={fileds.emergencySafe}>
												{emergencySafe.map((item, index) => {
													return (<Radio key={'emergencySafe' + index} value={item}>{item}</Radio>)
												})}
											</RadioGroup>
										</div>
									)}
								</Form.Item>
							</Col>
						</Row>
						<Row>
							<Col span={24} className="mb5">
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 5},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 19},
									}}
									label={``}>
									{getFieldDecorator(`state`, {
										initialValue: fileds.state,
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<div className="w">
											<RadioGroup onChange={this.handleChage.bind(this, "state")}
											            disabled={this.props.title == "edit"}
											            value={fileds.state}>
												{state.map((item, index) => {
													return (<Radio key={'state' + index} value={item}>{item}</Radio>)
												})}
											</RadioGroup>
										</div>
									)}
								</Form.Item>
							</Col>
						</Row>
						<Row>
							<Col span={12} className="mb5">
								<Form.Item label={``}>
									{getFieldDecorator(`createTime`, {
										initialValue: fileds.createTime == null ? "" : moment(fileds.createTime, 'YYYY-MM-DD'),
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<DatePicker
											format="YYYY-MM-DD"
											style={{width: '100%'}}
											value={fileds.createTime == null ? "" : moment(fileds.createTime, 'YYYY-MM-DD')}
											onChange={this.onDateChange.bind(this, 'createTime')}
										/>
									)}
								</Form.Item>
							</Col>
							<Col span={12} className="mb5">
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 12},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 12},
									}}
									label={``}>
									{getFieldDecorator(`planSolveTime`, {
										initialValue: fileds.planSolveTime == null ? "" : moment(fileds.planSolveTime, 'YYYY-MM-DD'),
										rules: [
											{
												required: true,
												message: '',
											},
										],
									})(
										<DatePicker
											format="YYYY-MM-DD"
											style={{width: '100%'}}
											value={fileds.planSolveTime == null ? "" : moment(fileds.planSolveTime, 'YYYY-MM-DD')}
											onChange={this.onDateChange.bind(this, 'planSolveTime')}
										/>
									)}
								</Form.Item>
							</Col>

						</Row>
						<Row style={{marginTop: '10px'}}>
							{/* <Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('handler', {
										initialValue: fileds.handler,
										rules: [{required: true, message: ''}],
									})(<Input
										onChange={this.handleChage.bind(this, "handler")}
									/>)}
								</Form.Item>
							</Col> */}
							<Col span={12}>
								<Form.Item label={``}>
									{getFieldDecorator('reviewer', {
										initialValue: fileds.reviewer,
										rules: [{required: true, message: ''}],
									})(<Input
										onChange={this.handleChage.bind(this, "reviewer")}
									/>)}
								</Form.Item>
							</Col>
							<Col span={12} className={fileds.state==""?"hidden":"mb5"}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 12},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 12},
									}}
									label={``}>
									{getFieldDecorator(`solveTime`, {
										initialValue: fileds.solveTime == null ? "" : moment(fileds.solveTime, 'YYYY-MM-DD'),
										rules: [
											{
												required: fileds.state==""?false:true,
												message: '',
											},
										],
									})(
										<DatePicker
											format="YYYY-MM-DD"
											style={{width: '100%'}}
											value={fileds.solveTime == null ? "" : moment(fileds.solveTime, 'YYYY-MM-DD')}
											onChange={this.onDateChange.bind(this, 'solveTime')}
										/>
									)}
								</Form.Item>
							</Col>
						</Row>
						<Row style={{marginTop: '10px'}}>
							<Col span={24}>
								<Form.Item
									labelCol={{
										xs: {span: 24},
										sm: {span: 5},
									}}
									wrapperCol={{
										xs: {span: 24},
										sm: {span: 19},
									}}
									label={``}>
									<TextArea autosize={{ minRows: 2}}
									          onChange={this.handleChage.bind(this, "remark")}
									          value={fileds.remark}
									/>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</Modal>
			</div>
		)
	}
}

const form = Form.create({})(Quality);
export default form;



