/*
*  store
*/
import {observable, action} from 'mobx';
import {requestPost} from "@/utils/api";
import Config from "@/config";
import Schemas from "@/utils/Schemas/construction";
import xmglSchemas from "@/utils/Schemas/xmgl";
import bpmSchemas from "@/utils/Schemas/bpm";
import moment from 'moment';

class ConstructionStore {
  @observable pageIndex = 1;
  @observable pageSize = 10;
  @observable totalCount = 0;
  @observable tableData = [];
  @observable orgUser = []; // ids
  @observable projectField={
    id:null,
    name:'',
    code:'',
    region:'',
    address:'',
    buildUnits:{ "id": "",
      "name": "",
      "type": 1,
      "orgUser": []},
    supervisionUnits:{"id": "",
      "name": "",
      "type": 2,
      "orgUser": []},
    constructionUnits:{"id": "",
      "name": "",
      "type": 3,
      "orgUser": []},
    subcontractors: [],
    foreman:'',
    isSave:1,
    createTime:null,
    isDeleted:1,
    longitude: null,
    latitude: null,
    state: "", // 123
    requiredTime: null,
    peopleCount:null, //
    positions: [], // 
    zplan: null, // 
    wisdomseeProjectId: '', //
  }; // 
  @observable projectId = ''; // id
  @observable buildUnits = []; // 
  @observable supervisionUnits = []; // 
  @observable constructionUnits = []; // 
  @observable cardpageIndex = 1;
  @observable cardpageSize = 10;
  @observable cardtotalCount = 0;
  @observable cardtableData = [];
  @observable material = {
    id:null,
    projectId:"",
    name:"",
    spec:"",
    manufacturer:null,
    certificateNumber:null,
    num:null,
    item:null,
    result:null,
    remark:null,
    reportId:null,
    totalNum:null,
    deliveryTime:null,
    units:null,
    attachments: [],
    "taskId":'',
    "expectResourceId": "",
    "resourceType": "",
    "dayTaskName": "",
  }; // 
  @observable expectId = "";

  // 
  @action getMaterialList() {
    return requestPost('getMaterialList', Config.construction.getMaterialList, Schemas.construction.getMaterialList, {
      projectId: this.projectId,
      text: '',
      pageIndex: this.cardpageIndex,
      pageSize: this.cardpageSize
    }).then((res)=>{
      if (res) {
        this.cardpageIndex = res.pageIndex;
        this.cardpageSize = res.pageSize;
        this.cardtotalCount = res.totalCount;
        this.cardtableData =Object.assign([], res.body.length?res.body.map((item,index)=>{return Object.assign(item, {key: index+1})}):[]);
      }
    });
  }

  // 
  @action getMaterial(param) {
    return requestPost('getMaterial', Config.construction.getMaterial, Schemas.construction.getMaterial, {
      id: param
    })
  }

  // 
  @action saveMaterial(param) {
    return requestPost('saveMaterial', Config.construction.saveMaterial, Schemas.construction.saveMaterial, {
      material: param
    })
  }

  // 
  @action delMaterial(param) {
    return requestPost('delMaterial', Config.construction.delMaterial, Schemas.construction.delMaterial, {
      id: param
    })
  }

  // 
  @action saveProject() {
    return requestPost('saveProject', Config.construction.saveProject, Schemas.construction.saveProject, {
      project: this.projectField
    })
  }

  // 
  @observable technology = {
    "id": null,
    "name": "",
    "ready": "",
    "content": "",
    "workingHours": 1
  };
  @observable technologypageIndex = 1;
  @observable technologypageSize = 10;
  @observable technologytotalCount = 0;
  @observable technologytableData = [];
  @observable name = '';

  // 
  @action getTechnologyList() {
    return requestPost('getTechnologyList', Config.xmgl.getTechnologyList, xmglSchemas.xmgl.getTechnologyList, {
      name: this.name,
      pageIndex: this.technologypageIndex,
      pageSize: this.technologypageSize
    }).then((res)=>{
      if (res) {
        this.technologypageIndex = res.pageIndex;
        this.technologypageSize = res.pageSize;
        this.technologytotalCount = res.totalCount;
        this.technologytableData = Object.assign([], res.body);
      }
    });
  }

  // 
  @action addTechnology() {
    return requestPost('saveTechnology', Config.xmgl.saveTechnology, xmglSchemas.xmgl.saveTechnology, {
      technology: this.technology
    })
  }

  // 
  @action delTechnology(param) {
    return requestPost('delTechnology', Config.xmgl.delTechnology, xmglSchemas.xmgl.delTechnology, {
      id: param
    })
  }

  @observable ZPlanList = [];
  // 
  @action getProjectZPlanList() {
    return requestPost('getProjectZPlanList', Config.construction.getProjectZPlanList, Schemas.construction.getProjectZPlanList, {
      text: ""
    }).then((res) => {
      if (Array.isArray(res)) {
         this.ZPlanList = Object.assign([], res);
      }
    });
  }
 // @observable wisdomseeProject = [];
  // 
  @action
  getAllProjectList(params) {
    return requestPost('getAllProjectList', Config.bpm.getAllProjectList, bpmSchemas.bpm.getAllProjectList, {
      beginTime: moment().startOf('year').valueOf(),
      endTime: moment().endOf('year').valueOf(),
      text: params.text,
      products:[],
      state:[],
      masterIds:[],
      pageIndex: params.pageIndex,
      pageSize:10
    })
  }
  @action
  getProjectInfoById(param) {
    return requestPost('getProjectInfoById', Config.bpm.getProjectInfoById, bpmSchemas.bpm.getProjectInfoById, {
      projectId: param
    })
  }
}


export default ConstructionStore;



