import React from 'react';
import {observer} from 'mobx-react';
import {Tabs,Breadcrumb} from 'antd';
import Person from '@/components/xmgl/Person';
import Quality from '@/components/xmgl/Quality';
import ItemSetting from '@/components/xmgl/ItemSetting';
import Mechanical from '@/components/xmgl/Mechanical';
//import DailyDiary from '@/components/xmgl/DailyDiary';
import Progress from '@/components/xmgl/Progress';
import Epidemic from '@/components/xmgl/Epidemic';
import Environment from '@/components/xmgl/Environment';
import MateriaModal from "@/components/construction/MateriaModal";

import XmglStore from '@/store/xmgl/XmglStore';
import ConstructionStore from '@/store/construction/ConstructionStore';
import * as mobx from 'mobx';
import url from 'url';

const {TabPane} = Tabs;

@observer
class Xmgl extends React.Component {
	constructor(props) {
		super(props);
		this.store = new XmglStore()
		this.conStore = new ConstructionStore()
		this.state = {
			key: "1", // tabs
		}
	}

	componentWillMount() {
		let search = this.props.location.search;
		const projectId = url.parse(search, true).query.projectId;
		const parojectName = url.parse(search, true).query.name;
		if (projectId !== undefined) {
			this.store.projectId = projectId;
			this.store.parojectName = parojectName;
			this.conStore.projectId = projectId;
			localStorage.setItem("projectId", projectId);
			localStorage.setItem("projectName", parojectName);
		} else {
			this.store.projectId = localStorage.getItem("projectId");
			this.store.parojectName = localStorage.getItem("projectName");
			this.conStore.projectId = localStorage.getItem("projectId");
		}
	}

	componentDidMount() {
		// sts
		this.store.getSts();
		this.child1.getTabeData();
	}

	componentWillUnmount() {

	}

	getPar(par) {
		//URL
		var local_url = document.location.href;
		//get
		var get = local_url.indexOf(par + "=");
		if (get == -1) {
			return false;
		}
		//
		var get_par = local_url.slice(par.length + get + 1);
		//get
		var nextPar = get_par.indexOf("&");
		if (nextPar != -1) {
			get_par = get_par.slice(0, nextPar);
		}
		return get_par;
	}

	callback = (key) => {
		this.setState({
			key
		},() => {
           if (key!=="8") {
	           this["child"+key].getTabeData();
           }
		});
	}

	render() {
		const {ossData} = this.store;
		const {key} = this.state;

		return(
			<div>
			<div className="con_bottom">
				<Breadcrumb separator=">" style={{margin: '0'}}>
					<Breadcrumb.Item href="#/bpm/construction"></Breadcrumb.Item>
					<Breadcrumb.Item></Breadcrumb.Item>
				</Breadcrumb>
			</div>
			<Tabs
				      className="mt10"
				      tabPosition={'left'}
				      activeKey={key}
				      onChange={this.callback}
				>
					<TabPane tab="" key="1">
						<Epidemic
							onRef={(ref) => {
								this.child1 = ref
							}}
							ossData={mobx.toJS(ossData)}
							store={this.store}/>
					</TabPane>
					<TabPane tab="" key="2">
						<Progress
							onRef={(ref) => {
								this.child2 = ref
							}}
							ossData={mobx.toJS(ossData)}
							store={this.store}/>
					</TabPane>
					<TabPane tab="" key="3">
						<Quality
							onRef={(ref) => {
								this.child3 = ref
							}}
							store={this.store}
						/>
					</TabPane>
					<TabPane tab="" key="4">
						<Person
							onRef={(ref) => {
								this.child4 = ref
							}}
							store={this.store}
						/>
					</TabPane>
					<TabPane tab="" key="5">
						<Mechanical
							onRef={(ref) => {
								this.child5 = ref
							}}
							store={this.store}/>
					</TabPane>
					<TabPane tab="" key="6">
						<MateriaModal
							onRef={(ref) => {
								this.child6 = ref
							}}
							ossData={mobx.toJS(ossData)}
							store={this.conStore}
						/>
					</TabPane>
					<TabPane tab="" key="7">
						<Environment
							onRef={(ref) => {
								this.child7 = ref
							}}
							store={this.store}
						/>
					</TabPane>
					{/*<TabPane tab="" key="8">
						<DailyDiary
							store={this.store}
						/>
					</TabPane>*/}
		            <TabPane tab="" key="8">
		              <ItemSetting
		                  store={this.store}
		              />
		            </TabPane>
				</Tabs></div>
		)
	}
}

export default Xmgl;



